<template>
  <ErrorBoundary>
    <div :class="`zero-code editor-container ${componentInstanceId}`">
      <!--  -->
      <TabNavigation :active-tab="activeTab || 'editor'" @update:active-tab="switchToTab" />

      <!--  -->
      <RichTextToolbar
        :show="showRichTextToolbar"
        :position="richTextPosition"
        :actions="richTextEditor.actions"
        :editing-state="richTextEditor.editingState"
        :is-editing="showRichTextToolbar"
        @execute-command="richTextEditor.executeCommand"
        @save="richTextEditor.stopEdit(true)"
        @cancel="richTextEditor.stopEdit(false)"
      />

      <div class="zero-code-editor-content">
        <!--  -->
        <EditorMainPanel
          v-if="activeTab === 'editor'"
          :data-tab="dataTab"
          :editable-page-data="editablePageData"
          :local-parts="localParts"
          :local-common-parts="editor.state.localCommonParts.value"
          :local-individual-parts="editor.state.localIndividualParts.value"
          :parts="props.parts || []"
          :common-images="commonImages"
          :individual-images="individualImages"
          :show-part-selector="showPartSelector"
          :show-nested-part-selector="showNestedPartSelector"
          :show-editor="showEditor"
          :selected-component="selectedComponent"
          :render-component-with-nested="renderComponentWithNested"
          :get-field-info="getFieldInfoWrapper"
          :should-show-basic-field="shouldShowBasicFieldWrapper"
          :should-show-array-field="shouldShowArrayFieldWrapper"
          :should-show-array-item-field="shouldShowArrayItemFieldWrapper"
          :get-input-type="getInputTypeWrapper"
          :is-checkbox-checked="isCheckboxCheckedWrapper"
          :parts-data="localParts && localParts.length > 0 ? localParts : props.parts || []"
          :is-text-editing="isTextEditing"
          @update:data-tab="value => (dataTab = value)"
          @add-part="openPartSelector"
          @edit-part="openEditor"
          @delete-part="deletePart"
          @reorder-parts="reorderParts"
          @close-part-selector="closePartSelector"
          @select-part="selectPart"
          @close-nested-part-selector="closeNestedPartSelector"
          @select-nested-part="selectNestedPart"
          @close-editor="closeEditor"
          @update-editor-value="handleUpdateEditorValue"
          @add-array-item="handleAddArrayItem"
          @remove-array-item="handleRemoveArrayItem"
          @update-array-item="handleUpdateArrayItem"
          @update-checkbox="handleUpdateCheckbox"
        />

        <!--  -->
        <PartsManagementPanel
          v-if="activeTab === 'parts'"
          :local-parts="localParts"
          :local-common-parts="editor.state.localCommonParts.value"
          :local-individual-parts="editor.state.localIndividualParts.value"
          :show-part-registration="showPartRegistration"
          :existing-part-types="existingPartTypes"
          :editing-part="editingPart"
          :render-part-preview="renderPartPreview"
          @update-parts="updatePartsData"
          @show-registration="openPartRegistration"
          @edit-part="openPartEditor"
          @delete-part="deletePartFromManager"
          @close-registration="closePartRegistration"
          @save-part="saveNewPart"
        />

        <!--  -->
        <PreviewPanel
          v-if="activeTab === 'preview'"
          :editable-page-data="editablePageData"
          :local-parts="localParts"
          :parts="props.parts || []"
          :common-images="commonImages"
          :individual-images="individualImages"
        />

        <!--  -->
        <ImageManagementPanel
          v-if="activeTab === 'images'"
          :common-images="commonImages"
          :individual-images="individualImages"
          @update-common-images="updateCommonImages"
          @update-individual-images="updateIndividualImages"
          @send-common-images-data="sendCommonImagesData"
          @send-individual-images-data="sendIndividualImagesData"
        />
      </div>

      <!--  -->
      <div class="zero-code-button-section">
        <button class="zero-code-send-btn zero-code-page-btn" @click="handleDataDisplay('page')">Page</button>
        <button class="zero-code-send-btn zero-code-common-parts-btn" @click="handleDataDisplay('commonParts')">Parts</button>
        <button class="zero-code-send-btn zero-code-individual-parts-btn" @click="handleDataDisplay('individualParts')">Parts</button>
        <button class="zero-code-send-btn zero-code-common-images-btn" @click="handleDataDisplay('commonImages')">
          
        </button>
        <button class="zero-code-send-btn zero-code-individual-images-btn" @click="handleDataDisplay('individualImages')">
          
        </button>
        <button class="zero-code-send-btn zero-code-reset-btn" @click="resetPageData"></button>
      </div>

      <!--  -->
      <div v-if="displayedData" class="zero-code-data-display-area">
        <div class="zero-code-data-display-header">
          <h4>{{ displayedDataTitle }}</h4>
          <button class="zero-code-close-button" @click="closeDataDisplay"></button>
        </div>
        <div class="zero-code-data-display-content">
          <pre class="zero-code-data-display-pre">{{ displayedDataFormatted }}</pre>
        </div>
        <div class="zero-code-data-display-footer">
          <button class="zero-code-btn zero-code-btn-secondary" @click="copyDataToClipboard"></button>
          <button class="zero-code-btn zero-code-btn-primary" @click="sendCurrentData"></button>
        </div>
      </div>

      <!--  -->
      <ImageSelectorModal
        :visible="showGlobalImageSelector"
        :common-images="commonImages as any"
        :individual-images="individualImages as any"
        :current-value="currentGlobalImageValue"
        @close="closeGlobalImageSelector"
        @select="onGlobalImageSelected"
        @upload="onGlobalImageUploaded"
      />
    </div>
  </ErrorBoundary>
</template>

<script setup lang="ts">
  import { ref, nextTick, watch, onMounted, onUnmounted, computed, reactive, triggerRef } from 'vue'

  // WebCSS
  import './styles/zero-code.css'

  // 
  import EditorMainPanel from './components/panels/EditorMainPanel.vue'
  import PartsManagementPanel from './components/panels/PartsManagementPanel.vue'
  import PreviewPanel from './components/panels/PreviewPanel.vue'
  import ImageManagementPanel from './components/panels/ImageManagementPanel.vue'

  // 
  import { useEditorTabs } from './composables/useEditorTabs'
  import { useZeroCode } from './composables/useZeroCode'
  import { useComponentUtils } from './composables/useComponentUtils'
  import { useCollapsibleItems } from './composables/useCollapsibleItems'
  import { useDataLoader } from './composables/useDataLoader'
  import { useImageDisplay } from './composables/useImageDisplay'

  // 
  import TabNavigation from '@shared/components/TabNavigation.vue'
  import ErrorBoundary from '@shared/components/ErrorBoundary.vue'
  import RichTextToolbar from './components/editor/RichTextToolbar.vue'
  import ImageSelectorModal from './components/editor/ImageSelectorModal.vue'

  // 
  import { logger } from '@shared/utils/logger'
  import { cleanupHtmlContent } from '@shared/utils/htmlHelpers'
  import { editorEventBus } from '@shared/utils/eventBus'

  // 
  import type { Component, PartSection, ImageData } from '@types'

  // Props
  export interface Props {
    parts?: PartSection[]
    commonParts?: PartSection[]
    individualParts?: PartSection[]
    page?: Component[]
    commonImages?: ImageData[]
    individualImages?: ImageData[]
  }

  const props = withDefaults(defineProps<Props>(), {
    parts: () => [],
    commonParts: () => [],
    individualParts: () => [],
    page: () => [],
    commonImages: () => [],
    individualImages: () => [],
  })

  // Emits
  const emit = defineEmits<{
    'update:parts': [parts: PartSection[]]
    'update:commonParts': [parts: PartSection[]]
    'update:individualParts': [parts: PartSection[]]
    'update:page': [page: Component[]]
    'update:commonImages': [images: ImageData[]]
    'update:individualImages': [images: ImageData[]]
  }>()

  // ID
  const componentInstanceId = ref(`zero-code-${Math.random().toString(36).substr(2, 9)}`)
  
  // 
  const dataLoader = useDataLoader()
  
  // 
  const { getImageSrc } = useImageDisplay()
  
  // 
  const allImages = computed(() => [...commonImages.value, ...individualImages.value])
  
  // HTMLURL
  const resolveImageUrlsInHTML = (html: string): string => {
    if (!html) return html
    
    // allImages.value
    const currentImages = allImages.value
    if (currentImages.length === 0) return html

    let processedHtml = html
    
    // imgsrc
    processedHtml = processedHtml.replace(
      /<img([^>]*?)src=["']([^"']+)["']([^>]*?)>/gi,
      (match, beforeSrc, originalSrc, afterSrc) => {
        // IDURL
        const imageData = currentImages.find(img => {
          // 
          if (img.id === originalSrc || img.url === originalSrc || img.name === originalSrc) {
            return true
          }
          
          // URL: /img/hero_image.jpg  hero_image.jpg
          if (originalSrc.endsWith('/' + img.name) || img.url.endsWith(originalSrc)) {
            return true
          }
          
          // 
          const originalFileName = originalSrc.split('/').pop()
          if (originalFileName === img.name) {
            return true
          }
          
          return false
        })
        
        if (imageData) {
          const resolvedSrc = getImageSrc(imageData)
          return `<img${beforeSrc}src="${resolvedSrc}"${afterSrc}>`
        }
        
        return match
      }
    )

    return processedHtml
  }
  
  // 
  const resetImageToOriginal = (imageId: string) => {
    // 
    const commonImageIndex = commonImages.value.findIndex(img => img.id === imageId)
    if (commonImageIndex !== -1) {
      const updatedImages = [...commonImages.value]
      updatedImages[commonImageIndex] = {
        ...updatedImages[commonImageIndex],
        fileData: {
          ...updatedImages[commonImageIndex].fileData,
          base64: '' // Base64
        }
      }
      updateCommonImages(updatedImages)
      return
    }
    
    // 
    const individualImageIndex = individualImages.value.findIndex(img => img.id === imageId)
    if (individualImageIndex !== -1) {
      const updatedImages = [...individualImages.value]
      updatedImages[individualImageIndex] = {
        ...updatedImages[individualImageIndex],
        fileData: {
          ...updatedImages[individualImageIndex].fileData,
          base64: '' // Base64
        }
      }
      updateIndividualImages(updatedImages)
    }
  }
  
  // 
  onMounted(async () => {
    try {
      // 
      const shouldLoadSampleData = process.env.NODE_ENV === 'development' || process.env.LOAD_SAMPLE_DATA === 'true'
      
      if (shouldLoadSampleData) {
        logger.info(' ...')
        await dataLoader.initialize()
        
        // localParts
        if (dataLoader.combinedParts.value && dataLoader.combinedParts.value.length > 0) {
          localParts.value = dataLoader.combinedParts.value
          logger.success(`  (${dataLoader.combinedParts.value.length})`)
        } else {
          logger.warn(' ')
        }
        
        // 
        if (dataLoader.defaultPageData.value && dataLoader.defaultPageData.value.length > 0) {
          editablePageData.value = dataLoader.defaultPageData.value
          logger.success(`  (${dataLoader.defaultPageData.value.length})`)
        } else {
          logger.warn(' ')
        }
        
        // 
        if (dataLoader.images.value) {
          commonImages.value = dataLoader.images.value.common as any[]
          individualImages.value = dataLoader.images.value.individual as any[]
          logger.success(`  (:${dataLoader.images.value.common.length}, :${dataLoader.images.value.individual.length})`)
        } else {
          logger.warn(' ')
        }
        
        logger.success(' ')
      } else {
        logger.info(' ')
        // 
        localParts.value = []
        editablePageData.value = []
        commonImages.value = []
        individualImages.value = []
        logger.success(' ')
      }
      
      // index.html
      nextTick(() => {
        const event = new CustomEvent('zero-code-initialized', {
          detail: {
            partsCount: localParts.value?.length || 0,
            pageCount: editablePageData.value?.length || 0
          }
        })
        document.dispatchEvent(event)
        logger.debug(' zero-code-initialized ')
      })
      
    } catch (error) {
      logger.error(' :', error)
    }
  })

  // 
  const editor = useZeroCode(props, emit)

  // 
  const selectedComponent = editor.state.selectedComponent
  const selectedComponentIndex = editor.state.selectedComponentIndex
  const editablePageData = editor.state.editablePageData
  const localParts = editor.state.localParts
  const dataTab = editor.state.dataTab

  // 
  const showRichTextToolbar = ref(false)
  const richTextPosition = ref({ top: 0, left: 0 })
  const currentEditingElement = ref<HTMLElement | null>(null)
  const originalContent = ref('')
  const currentComponentId = ref('')
  const currentParamName = ref('')

  // 
  const isTextEditing = computed(() => currentEditingElement.value !== null)

  // 
  const showGlobalImageSelector = ref(false)
  const currentGlobalImageValue = ref('')
  const currentGlobalImageComponent = ref<Component | null>(null)
  const currentGlobalImageField = ref('')

  const richTextEditor = reactive({
    showToolbar: computed(() => showRichTextToolbar.value),
    position: computed(() => richTextPosition.value),
    actions: [
      { name: 'bold', icon: 'B', title: '', command: 'bold' },
      { name: 'italic', icon: 'I', title: '', command: 'italic' },
      { name: 'underline', icon: 'U', title: '', command: 'underline' },
      { name: 'separator' },
      { name: 'link', icon: '', title: '', command: 'link' },
    ],
    editingState: {},
    executeCommand: (command: string) => {
      logger.debug(':', command)
      executeRichTextCommand(command)
    },
    stopEdit: (save: boolean) => {
      logger.debug(':', save)
      stopRichTextEdit(save)
    },
  })

  // 
  const executeRichTextCommand = (command: string) => {
    if (!currentEditingElement.value) return

    logger.debug(' :', command)

    try {
      if (command === 'link') {
        // 
        const url = prompt('URL:', 'https://')
        if (url && url.trim()) {
          const success = document.execCommand('createLink', false, url)
          logger.debug(' :', success ? '' : '')
        }
      } else {
        // 
        const success = document.execCommand(command, false, undefined)
        logger.debug(' :', command, success ? '' : '')
      }

      // 
      logger.debug(' ')
      // updateComponentDataSync()
    } catch (error) {
      logger.error(' execCommand:', error)
    }
  }


  // HTML
  const updateComponentDataSync = () => {
    if (!currentEditingElement.value || !currentComponentId.value || !currentParamName.value) return

    const rawContent = currentEditingElement.value.innerHTML
    const cleanedContent = cleanupHtmlContent(rawContent)

    // z-for

    // editablePageData
    const updateComponent = (components: Component[]): boolean => {
      // z-forID
      
      // IDz-for
      const parseZForId = (id: string) => {
        // 1: comp_[parentId]_[arrayName]_[index]_[paramName] 
        const pattern1 = /^(.+)_([a-zA-Z_]+)_(\d+)_([a-zA-Z_]+)$/
        const match1 = id.match(pattern1)
        
        if (match1) {
          const [, parentId, arrayName, arrayIndex, paramName] = match1
          return {
            parentId,
            arrayName,
            arrayIndex: parseInt(arrayIndex),
            paramName
          }
        }
        
        // 2: ID
        const parts = id.split('_')
        
        if (parts.length >= 5) {
          // paramName
          const paramName = parts[parts.length - 1]
          
          // 2index
          const arrayIndex = parts[parts.length - 2]
          if (!/^\d+$/.test(arrayIndex)) {
            return null
          }
          
          // 3
          const arrayName = parts[parts.length - 3]
          
          // parentId
          const parentId = parts.slice(0, -3).join('_')
          
          return {
            parentId,
            arrayName,
            arrayIndex: parseInt(arrayIndex),
            paramName
          }
        }
        
        return null
      }

      const zForInfo = parseZForId(currentComponentId.value)
      
      // z-for

      if (zForInfo) {
        const { parentId, arrayName, arrayIndex, paramName } = zForInfo

        // 
        const findTargetArrayItem = (components: Component[], targetParentId: string): any => {
          for (const comp of components) {
            // 
            if (comp.id === targetParentId) {
              if (Array.isArray(comp[arrayName])) {
                return comp[arrayName][arrayIndex]
              }
            }
            
            // nested_parts
            if (comp.nested_parts && Array.isArray(comp.nested_parts)) {
              const found = findTargetArrayItem(comp.nested_parts, targetParentId)
              if (found) return found
            }
            
            // z-for
            for (const [propKey, propValue] of Object.entries(comp)) {
              if (Array.isArray(propValue) && propKey !== 'nested_parts') {
                // ID
                for (let i = 0; i < propValue.length; i++) {
                  const arrayItem = propValue[i]
                  if (arrayItem && typeof arrayItem === 'object') {
                    // ID
                    const itemId = `${comp.id}_${propKey}_${i}`
                    if (itemId === targetParentId) {
                      if (Array.isArray(arrayItem[arrayName])) {
                        return arrayItem[arrayName][arrayIndex]
                      }
                    }
                    
                    // 
                    for (const [slotKey, slotValue] of Object.entries(arrayItem)) {
                      if (Array.isArray(slotValue)) {
                        const found = findTargetArrayItem(slotValue, targetParentId)
                        if (found) return found
                      }
                    }
                  }
                }
              }
            }
          }
          return null
        }

        const targetArrayItem = findTargetArrayItem(components, parentId)
        if (targetArrayItem && typeof targetArrayItem === 'object') {
          targetArrayItem[paramName] = cleanedContent
          return true
        }
        return false
      }

      for (const component of components) {
        if (component.id === currentComponentId.value) {
          ;(component as any)[currentParamName.value] = cleanedContent
          return true
        }

        // nested_parts
        if (component.nested_parts && Array.isArray(component.nested_parts)) {
          logger.debug(` nested_parts: ${component.id}`)
          if (updateComponent(component.nested_parts)) {
            return true
          }
        }

        // z-for
        for (const [key, value] of Object.entries(component)) {
          if (Array.isArray(value) && key !== 'nested_parts') {
            logger.debug(` : ${component.id}.${key}`)
            // nested_parts/nested_slots
            for (const arrayItem of value) {
              if (arrayItem && typeof arrayItem === 'object') {
                if ((arrayItem as any).id === currentComponentId.value) {
                  ;(arrayItem as any)[currentParamName.value] = cleanedContent
                  logger.debug(' :', arrayItem)
                  return true
                }
                // 
                for (const [slotKey, slotValue] of Object.entries(arrayItem)) {
                  if (Array.isArray(slotValue)) {
                    logger.debug(` : ${component.id}.${key}[].${slotKey}`)
                    if (updateComponent(slotValue)) {
                      return true
                    }
                  }
                }
              }
            }
          }
        }
      }
      logger.debug(` : ${currentComponentId.value}`)
      return false
    }

    if (updateComponent(editablePageData.value)) {
      // HTML
      if (currentEditingElement.value && rawContent !== cleanedContent) {
        currentEditingElement.value.innerHTML = cleanedContent
      }

      // 
      emit('update:page', editablePageData.value)
    }
  }

  // 
  const stopRichTextEdit = (save: boolean) => {
    if (!currentEditingElement.value) return

    logger.debug(' :', save ? '' : '')

    if (save) {
      // 
      updateComponentDataSync()
      logger.debug(' ')
    } else {
      // 
      currentEditingElement.value.innerHTML = originalContent.value
      logger.debug(' ')
    }

    // 
    currentEditingElement.value.contentEditable = 'false'
    currentEditingElement.value.classList.remove('editing')

    // user-select
    currentEditingElement.value.style.userSelect = ''
    currentEditingElement.value.style.webkitUserSelect = ''
    logger.debug(' ')

    logger.debug(' ')

    // 
    if ((currentEditingElement.value as any)._cleanup) {
      ;(currentEditingElement.value as any)._cleanup()
      delete (currentEditingElement.value as any)._cleanup
    }

    // 
    currentEditingElement.value = null
    originalContent.value = ''
    currentComponentId.value = ''
    currentParamName.value = ''

    // 
    showRichTextToolbar.value = false
    logger.debug(' ')

    // Tiptap
    // editor.actions.stopTiptapEdit()
  }

  // 
  const openEditor = (index: number) => {
    editor.actions.openEditor(index)
  }
  const closeEditor = () => {
    // 
    const currentComponent = selectedComponent.value
    if (currentComponent) {
      logger.debug(' ')
      editor.actions.saveComponent(currentComponent)
    }
    editor.closeEditor()
  }
  const deletePart = (index: number) => {
    editor.actions.deletePart(index)
  }
  const reorderParts = (newOrder: Component[]) => {
    editor.actions.reorderParts(newOrder)
  }

  // 
  const reorderNestedParts = (pathString: string, newOrder: Component[]) => {
    logger.debug(' :', { pathString, newOrderCount: newOrder.length })
    editor.actions.reorderNestedParts(pathString, newOrder)
  }

  // &
  const setupNestedDragAndDrop = () => {
    const container = document.querySelector(`.${componentInstanceId.value}`)
    if (!container) return

    let draggedElement: HTMLElement | null = null
    let draggedData: {
      componentId: string
      path: string
      parentPath: string
      index: number
    } | null = null

    // 
    const handleDragStart = (e: Event) => {
      const event = e as DragEvent
      const target = event.target as HTMLElement

      if (target.classList.contains('draggable-nested')) {
        // 
        if (currentEditingElement.value) {
          logger.debug(' ')
          event.preventDefault()
          return
        }

        // z-forz-for
        if (target.classList.contains('z-for-nested-part')) {
          logger.debug(' z-for - z-for')
          return
        }

        draggedElement = target

        const componentId = target.dataset.componentId || ''
        const path = target.dataset.path || ''
        const parentPath = target.dataset.parentPath || ''
        const index = parseInt(target.dataset.index || '0')

        draggedData = { componentId, path, parentPath, index }

        if (event.dataTransfer) {
          event.dataTransfer.effectAllowed = 'move'
          event.dataTransfer.setData('text/plain', 'nested-component')
        }

        target.style.opacity = '0.5'
        logger.debug(' :', draggedData)
      }
    }

    const handleDragOver = (e: Event) => {
      const event = e as DragEvent
      event.preventDefault()
      if (event.dataTransfer) {
        event.dataTransfer.dropEffect = 'move'
      }
    }

    const handleDrop = (e: Event) => {
      const event = e as DragEvent
      event.preventDefault()

      if (!draggedElement || !draggedData) return

      const target = event.target as HTMLElement
      const dropTarget = target.closest('.draggable-nested') as HTMLElement

      if (dropTarget && dropTarget !== draggedElement) {
        const targetParentPath = dropTarget.dataset.parentPath || ''
        const targetIndex = parseInt(dropTarget.dataset.index || '0')

        logger.debug(' :', {
          draggedParentPath: draggedData.parentPath,
          draggedIndex: draggedData.index,
          targetParentPath,
          targetIndex,
        })

        // 
        if (draggedData.parentPath === targetParentPath) {
          logger.debug(' ')
          // ID
          editor.actions.moveNestedPartById(draggedData.parentPath, draggedData.index, targetIndex)
        } else {
          logger.debug(' ')
        }
      }

      // 
      if (draggedElement) {
        draggedElement.style.opacity = '1'
      }
      draggedElement = null
      draggedData = null
    }

    const handleDragEnd = (e: Event) => {
      const target = e.target as HTMLElement
      if (target.classList.contains('draggable-nested')) {
        target.style.opacity = '1'
      }
      draggedElement = null
      draggedData = null
    }

    // 
    container.addEventListener('dragstart', handleDragStart)
    container.addEventListener('dragover', handleDragOver)
    container.addEventListener('drop', handleDrop)
    container.addEventListener('dragend', handleDragEnd)

    logger.debug(' &')

    // 
    return () => {
      container.removeEventListener('dragstart', handleDragStart)
      container.removeEventListener('dragover', handleDragOver)
      container.removeEventListener('drop', handleDrop)
      container.removeEventListener('dragend', handleDragEnd)
      logger.debug(' &')
    }
  }
  // 
  const displayedData = ref<any>(null)
  const displayedDataTitle = ref('')
  const displayedDataType = ref('')

  // 
  const handleDataDisplay = (dataType: string) => {
    let data: any = null
    let title = ''
    
    switch (dataType) {
      case 'page':
        data = editablePageData.value
        title = 'Page'
        break
      case 'commonParts':
        data = editor.state.localCommonParts.value
        // localParts
        if (!data || data.length === 0) {
          data = localParts.value?.map(section => ({
            ...section,
            module: section.module?.filter(part => !part.category || part.category === 'common')
          })).filter(section => section.module && section.module.length > 0) || []
        }
        // props
        if (!data || data.length === 0) {
          data = props.parts?.map(section => ({
            ...section,
            module: section.module?.filter(part => !part.category || part.category === 'common')
          })).filter(section => section.module && section.module.length > 0) || []
        }
        title = 'Parts'
