  // 
  const handleUpdateEditorValue = (key: string, value: any) => {
    const component = selectedComponent.value
    if (component) {
      component[key] = value

      // z-for
      if ((component as any)._isZForItem) {
        const parentComponentId = (component as any)._parentComponentId
        const arrayName = (component as any)._arrayName
        const arrayIndex = (component as any)._arrayIndex

        logger.debug(
          ` z-for: ${parentComponentId}.${arrayName}[${arrayIndex}].${key} = ${value}`,
          {
            beforeUpdate: (component as any)[key],
            afterUpdate: value,
            componentId: component.id,
            isEditableItem: true
          }
        )

        // z-for
        const findComponentRecursively = (components: any[], targetId: string): any => {
          for (const comp of components) {
            if (comp.id === targetId) {
              return comp
            }
            // nested_parts
            if (comp.nested_parts && Array.isArray(comp.nested_parts)) {
              const found = findComponentRecursively(comp.nested_parts, targetId)
              if (found) return found
            }
            // z-for
            for (const key of Object.keys(comp)) {
              if (Array.isArray(comp[key])) {
                for (const item of comp[key]) {
                  if (item && typeof item === 'object' && item.nested_parts) {
                    const deepNested = findComponentRecursively(item.nested_parts, targetId)
                    if (deepNested) {
                      return deepNested
                    }
                  }
                }
              }
            }
          }
          return null
        }

        const parentComponent = findComponentRecursively(editablePageData.value, parentComponentId)
        if (parentComponent && Array.isArray(parentComponent[arrayName])) {
          const array = parentComponent[arrayName]
          
          logger.debug(` handleUpdateEditorValue: `, {
            parentComponentId,
            parentComponentFound: !!parentComponent,
            parentComponentType: parentComponent?.type,
            arrayName,
            arrayFound: Array.isArray(parentComponent[arrayName]),
            arrayLength: array.length,
            arraySnapshot: array.map(item => ({ 
              id: item.id, 
              stableItemId: item.stableItemId, 
              card_title: item.card_title 
            })),
            editingKey: key,
            editingValue: value,
            editingComponentId: component.id
          })
          
          // arrayIndexstableItemId
          let actualIndex: number = -1
          if (typeof arrayIndex === 'string') {
            actualIndex = array.findIndex((item: any) => 
              item.stableItemId === arrayIndex || item.id === arrayIndex
            )
            logger.debug(` stableItemId "${arrayIndex}"  ${actualIndex} `)
          } else if (typeof arrayIndex === 'number') {
            actualIndex = arrayIndex
          }
          
          if (actualIndex >= 0 && actualIndex < array.length) {
            // 
            const beforeValue = array[actualIndex][key]
            array[actualIndex][key] = value
            logger.debug(
              ` z-for: ${parentComponentId}.${arrayName}[${actualIndex}].${key}`,
              {
                beforeValue,
                afterValue: array[actualIndex][key],
                actualIndex,
                arrayLength: array.length,
                fullArrayItem: array[actualIndex],
                arrayItemId: array[actualIndex].id,
                editingComponentId: component.id,
                parentComponentFound: !!parentComponent,
                parentComponentId: parentComponent?.id
              }
            )
            
            // 
            if (array[actualIndex][key] === value) {
              logger.success(` : ${key}="${value}" `)
            } else {
              logger.error(` : ${key}="${value}" `)
            }
          } else {
            logger.error(` z-for: ${arrayIndex}`, {
              arrayIndex,
              arrayLength: array.length,
              availableIds: array.map((item: any) => ({ id: item.id, stableItemId: item.stableItemId }))
            })
          }
        }
      }

      emit('update:page', editablePageData.value)
    }
  }

