    console.log(` [ZeroCode] [${index}]:`, array[index])

    // 
    const cleanItem = { ...item }
    delete cleanItem._isZForItem
    delete cleanItem._parentComponentId
    delete cleanItem._arrayName
    delete cleanItem._arrayIndex
    delete cleanItem._zForTemplate
    
    // idstableItemId
