      // alt
      const altFieldKey = currentGlobalImageField.value + '_alt'
      if (altFieldKey !== currentGlobalImageField.value && currentGlobalImageComponent.value[altFieldKey] !== undefined) {
        currentGlobalImageComponent.value[altFieldKey] = imageAlt
      }
      
      // 
      emit('update:page', editablePageData.value)
      
      // 
      nextTick(() => {
        triggerRef(editablePageData)
        // 
        if (localParts.value) {
          triggerRef(localParts)
        }
      })
      
      logger.debug(' :', imageUrl.substring(0, 50) + '...')
    }
    closeGlobalImageSelector()
  }
  
  // 

  // 
  const handleComponentClick = (e: Event) => {
    const clickedElement = e.target as HTMLElement
    
    // 
    const componentElement = document.querySelector(`.${componentInstanceId.value}`)
    if (!componentElement || !componentElement.contains(clickedElement)) {
      return // 
    }

    // 
    if (clickedElement.tagName === 'IMG') {
      const img = clickedElement as HTMLImageElement
      console.log(' :', {
        imgSrc: img.src.substring(img.src.lastIndexOf('/') + 1),
        hasEditableClass: img.classList.contains('zero-code-editable-image'),
        allClasses: Array.from(img.classList),
        componentId: img.closest('[data-component-id]')?.getAttribute('data-component-id')
      })
    }

    // 
    if (clickedElement.classList.contains('zero-code-image-edit-btn')) {
      e.preventDefault()
      e.stopPropagation()
      
      // 
      const img = clickedElement.previousElementSibling as HTMLImageElement
      if (!img || img.tagName !== 'IMG') {
        console.log(' ')
        return
      }
      
      const componentId = img.closest('[data-component-id]')?.getAttribute('data-component-id')
      let fieldName = img.getAttribute('data-field') || 'image'
      
      console.log(' :', {
        imgSrc: img.src.substring(img.src.lastIndexOf('/') + 1),
        hasEditableClass: img.classList.contains('zero-code-editable-image'),
        componentId,
        fieldName,
        availableComponents: editablePageData.value.map(c => c.id)
      })
      
      let component: Component | null = null
      
      if (componentId) {
        // 
        const findComponentById = (components: Component[], targetId: string): Component | null => {
          for (const comp of components) {
            if (comp.id === targetId) return comp
            if (comp.nested_parts && comp.nested_parts.length > 0) {
              const nested = findComponentById(comp.nested_parts, targetId)
              if (nested) return nested
            }
          }
          return null
        }
        
        component = findComponentById(editablePageData.value, componentId)
      } else {
        // componentIdsrc
        console.log(' componentId ')
        
        // src
        const imgSrc = img.src
        console.log(' src:', imgSrc)
        
        for (const comp of editablePageData.value) {
          console.log(`  ${comp.id} :`, comp)
          
          // 
          for (const [key, value] of Object.entries(comp)) {
            if (typeof value === 'string' && (
              key.toLowerCase().includes('image') || 
              key.toLowerCase().includes('url') ||
              key.toLowerCase().includes('icon')
            )) {
              console.log(` : ${key} = ${value}`)
              
              // src
              if (value && (imgSrc.includes(value) || value.includes(imgSrc.split('/').pop() || ''))) {
                console.log(` ! : ${comp.id}, : ${key}`)
                component = comp
                // 
                fieldName = key
                break
              }
            }
          }
          
          if (component) break
        }
        
        // 
        if (!component && editablePageData.value.length > 0) {
          component = editablePageData.value[0]
          console.log(' :', component.id)
        }
      }
      
      if (component) {
        // 
        currentGlobalImageComponent.value = component
        currentGlobalImageField.value = fieldName
        currentGlobalImageValue.value = img.src
        showGlobalImageSelector.value = true
        return
      } else {
        console.log(' ')
      }
    }

    // 
    if (!currentEditingElement.value || !showRichTextToolbar.value) return

    // 
    if (
      clickedElement.closest('.zero-code-rich-text-toolbar') ||
      clickedElement === currentEditingElement.value ||
      currentEditingElement.value.contains(clickedElement)
    ) {
      return
    }

    logger.debug(' ')
    stopRichTextEdit(true)
  }

  // &
  let cleanupNestedDragAndDrop: (() => void) | null = null
  
  // 
  let imageClickHandler: ((e: Event) => void) | null = null

  // 
  onMounted(async () => {
    logger.debug(' ZeroCode')


    // 
    document.addEventListener('click', handleComponentClick)
    logger.debug(' ')

    // CSS
    logger.debug(' CSS')

    // 
    logger.debug(' ...')
    editor.setupEventListeners()

    // EventBus
    logger.debug(' EventBus...')
    editorEventBus.on('reorderZForArrayItem', ({ componentId, arrayName, fromIndex, toIndex }) => {
      logger.debug(' z-for:', { componentId, arrayName, fromIndex, toIndex })
      
      // 
      const findComponentById = (components: Component[], targetId: string): Component | null => {
        for (const comp of components) {
          if (comp.id === targetId) {
            return comp
          }
          
          // 
          if (comp.nested_parts && comp.nested_parts.length > 0) {
            const nested = findComponentById(comp.nested_parts, targetId)
            if (nested) return nested
          }
          
          // z-for
          for (const key of Object.keys(comp)) {
            if (Array.isArray(comp[key])) {
              for (let index = 0; index < comp[key].length; index++) {
                const item = comp[key][index]
                if (item && typeof item === 'object' && item.nested_parts) {
                  const deepNested = findComponentById(item.nested_parts, targetId)
                  if (deepNested) {
                    return deepNested
                  }
                }
              }
            }
          }
        }
        return null
      }
      
      const targetComponent = findComponentById(editablePageData.value, componentId)
      
      if (!targetComponent) {
        logger.warn(':', { componentId, arrayName, availableIds: editablePageData.value.map(c => c.id) })
        return
      }
      
      //  arrayFields 
      let arrayItems: any[] | null = null
      
      if (targetComponent.arrayFields) {
        const arrayFields = targetComponent.arrayFields as Record<string, any[]>
        arrayItems = arrayFields[arrayName]
      }
      
      // arrayFields 
      if (!arrayItems && (targetComponent as any)[arrayName]) {
        arrayItems = (targetComponent as any)[arrayName]
      }
      
      if (!arrayItems || !Array.isArray(arrayItems)) {
        logger.warn(':', { 
          componentId, 
          arrayName, 
          foundDirectly: !!(targetComponent as any)[arrayName],
          foundInArrayFields: !!(targetComponent.arrayFields && (targetComponent.arrayFields as any)[arrayName]),
          itemType: typeof arrayItems
        })
        return
      }
      if (fromIndex >= 0 && toIndex >= 0 && fromIndex < arrayItems.length && toIndex < arrayItems.length) {
        const [movedItem] = arrayItems.splice(fromIndex, 1)
        arrayItems.splice(toIndex, 0, movedItem)
        logger.debug(' z-for:', { arrayName, from: fromIndex, to: toIndex })
      } else {
        logger.warn(':', { fromIndex, toIndex, arrayLength: arrayItems.length })
      }
    })

    // startTiptapEdit
    logger.debug(' ...')
    editor.exposeGlobalFunctions((componentId: string, paramName: string) => {
      logger.debug(' Tiptap:', { componentId, paramName })
      showRichTextToolbar.value = true
      editor.actions.startTiptapEdit(componentId, paramName, [], 0)
    })

    // 
    if (typeof window !== 'undefined') {
      ;(window as any).testAddImageButtons = () => {
        logger.debug(' ')
        // addImageEditButtons() // CSS
      }
      
      // 
      
      ;(window as any).testImageInfo = () => {
        const componentElement = document.querySelector(`.${componentInstanceId.value}`)
        if (!componentElement) return
        const allImages = componentElement.querySelectorAll('img')
        logger.debug(' ', allImages.length)
        allImages.forEach((img, index) => {
          const rect = img.getBoundingClientRect()
          logger.debug(` ${index}:`, {
            src: img.src.substring(img.src.lastIndexOf('/') + 1),
            parent: img.parentElement?.tagName,
            hasDataId: !!img.closest('[data-component-id]'),
            visible: rect.width > 0 && rect.height > 0,
            rect: { top: rect.top, left: rect.left, width: rect.width, height: rect.height }
          })
        })
        
        const floatingButtons = document.querySelectorAll('.zero-code-image-floating-button')
        logger.debug(' :', floatingButtons.length)
      }
    }
    
    // z-for
    if (typeof window !== 'undefined') {
      ;(window as any).getLatestComponentData = (componentId: string) => {
        // ID
        const component = editablePageData.value.find(c => c.id === componentId)
        if (component) {
          // console.log(`[Z-FOR] ${componentId}:`, Object.keys(component).filter(k => Array.isArray(component[k])))
          return component
        }
        
        // z-forID: div_nested_zfor_001_outer_items_0
        // : ID__
        const zForPattern = /^(.+?)_([^_]+_[^_]+)_(\d+)$/
        const match = componentId.match(zForPattern)
        if (match) {
          const [, baseId, arrayName, arrayIndexStr] = match
          const arrayIndex = parseInt(arrayIndexStr, 10)
          
          const parentComponent = editablePageData.value.find(c => c.id === baseId)
          if (parentComponent && Array.isArray(parentComponent[arrayName]) && parentComponent[arrayName][arrayIndex]) {
            const arrayElement = parentComponent[arrayName][arrayIndex]
            const childData = {
              ...parentComponent,  // 
              ...arrayElement,     // 
              type: parentComponent.type,
              module_name: parentComponent.module_name,
              id: componentId,
            }
            // console.log(`[Z-FOR] z-for: ${componentId}`)
            // console.log(`[Z-FOR] - ID: ${baseId}`)
            // console.log(`[Z-FOR] - : ${arrayName}`)
            // console.log(`[Z-FOR] - : ${arrayIndex}`)
            // console.log(`[Z-FOR] - :`, JSON.stringify(arrayElement))
            // console.log(`[Z-FOR] - :`, Object.keys(childData).filter(k => Array.isArray(childData[k])))
            return childData
          }
        }
        
        return null
      }
    }

    // 
    ;(window as any).toggleNestedCollapse = (pathString: string) => {
      logger.debug(' :', pathString)
      nestedCollapsible.toggleCollapse(pathString)
      
      // UI
      nextTick(() => {
        const contentElement = document.getElementById(`nested-content-${pathString}`)
        const buttonElement = document.querySelector(`[onclick="window.toggleNestedCollapse('${pathString}')"]`)
        
        if (contentElement && buttonElement) {
          const isCollapsed = nestedCollapsible.isCollapsed(pathString)
          
          if (isCollapsed) {
            contentElement.style.display = 'none'
            buttonElement.textContent = ''
            buttonElement.setAttribute('title', '')
          } else {
            contentElement.style.display = 'block'
            buttonElement.textContent = ''
            buttonElement.setAttribute('title', '')
          }
        }
      })
    }

    ;(window as any).isNestedCollapsed = (pathString: string) => {
      return nestedCollapsible.isCollapsed(pathString)
    }

    // z-for
    ;(window as any).reorderZForArrayItem = (
      componentId: string,
      arrayName: string,
      fromIndex: number,
      toIndex: number
    ) => {
      logger.debug(' reorderZForArrayItem :', { componentId, arrayName, fromIndex, toIndex })
      editorEventBus.emit('reorderZForArrayItem', { componentId, arrayName, fromIndex, toIndex })
    }

    // &
    cleanupNestedDragAndDrop = setupNestedDragAndDrop() || null

    // startRichTextEdit
    ;(window as any).startRichTextEdit = async (
      componentId: string,
      paramName: string,
      currentValue: string,
      element?: HTMLElement
    ) => {
      logger.debug(' :', { componentId, paramName, currentValue })

      // 
      const targetElement = element || (window.event?.target as HTMLElement)
      if (currentEditingElement.value === targetElement) {
        logger.debug(' ')
        return
      }

      if (targetElement) {
        // 
        if (currentEditingElement.value) {
          logger.debug(' ')
          stopRichTextEdit(false)
        }

        // 
        logger.debug(' ')

        currentEditingElement.value = targetElement
        originalContent.value = targetElement.innerHTML
        currentComponentId.value = componentId
        currentParamName.value = paramName

        // nextTick
        await nextTick()

        // 
        targetElement.contentEditable = 'true'
        logger.debug(' contentEditable:', targetElement.contentEditable)

        // user-select
        targetElement.style.userSelect = 'text'
        targetElement.style.webkitUserSelect = 'text'
        logger.debug(' user-select:', targetElement.style.userSelect)

        // CSS
        targetElement.classList.add('editing')
        logger.debug(' editing')

        logger.debug(' ')

        // 
        const handleBlur = (e: Event) => {
          const relatedTarget = (e as FocusEvent).relatedTarget as HTMLElement
          if (relatedTarget && relatedTarget.closest('.zero-code-rich-text-toolbar')) {
            logger.debug(' ')
            return
          }

          logger.debug(' ')
          // 
          setTimeout(() => {
            if (currentEditingElement.value === targetElement) {
              stopRichTextEdit(true) // 
            }
          }, 150)
        }

        targetElement.addEventListener('blur', handleBlur)
        logger.debug(' ')

        // 
        ;(targetElement as any)._cleanup = () => {
          targetElement.removeEventListener('blur', handleBlur)
          logger.debug(' ')
        }

        // 
        logger.debug(' ')
        const rect = targetElement.getBoundingClientRect()
        const scrollTop = window.pageYOffset || document.documentElement.scrollTop
        const scrollLeft = window.pageXOffset || document.documentElement.scrollLeft

        richTextPosition.value = {
          top: rect.top + scrollTop - 80,
          left: rect.left + scrollLeft,
        }
        logger.debug(' :', richTextPosition.value)

        // 
        logger.debug(' ')
        showRichTextToolbar.value = true
        logger.debug(' ')

        // 
        await nextTick()

        // 
        setTimeout(() => {
          if (targetElement && currentEditingElement.value === targetElement) {
            targetElement.focus()

            // 
            const range = document.createRange()
            const selection = window.getSelection()

            if (selection && targetElement.childNodes.length > 0) {
              // 
              const lastNode = targetElement.childNodes[targetElement.childNodes.length - 1]
              if (lastNode.nodeType === Node.TEXT_NODE) {
                range.setStart(lastNode, lastNode.textContent?.length || 0)
              } else {
                range.setStartAfter(lastNode)
              }
              range.collapse(true)

              selection.removeAllRanges()
              selection.addRange(range)

              logger.debug(' ')
            } else if (selection) {
              // 
              range.selectNodeContents(targetElement)
              range.collapse(false)
              selection.removeAllRanges()
              selection.addRange(range)

              logger.debug(' ')
            }

            logger.debug(' ')
          }
        }, 10)
      }

      // Tiptap
      // editor.actions.startTiptapEdit(componentId, paramName, [], 0)
    }

    // : localParts
    logger.debug('localParts.value:', localParts.value)
    logger.debug('props.parts:', props.parts)

    // 
    logger.debug(' :')
    logger.debug('  openNestedPartSelector:', typeof window.openNestedPartSelector)
    logger.debug('  openNestedEditor:', typeof window.openNestedEditor)

    logger.debug(' ZeroCode')
  })

  // Web
  defineExpose({
    parts: computed(() => localParts.value && localParts.value.length > 0 ? localParts.value : props.parts || []),
    page: computed(() => editablePageData.value || [])
  })

  // 
  onUnmounted(() => {
    logger.debug(' ZeroCode')
    document.removeEventListener('click', handleComponentClick)
    if (imageClickHandler) {
      document.removeEventListener('click', imageClickHandler)
    }
    
    // 
    const componentElement = document.querySelector(`.${componentInstanceId.value}`)
    const floatingButtons = componentElement ? componentElement.querySelectorAll('.zero-code-image-floating-button') : []
    floatingButtons.forEach(button => button.remove())
    
    if (cleanupNestedDragAndDrop) {
      cleanupNestedDragAndDrop()
    }
    
    // EventBus
    editorEventBus.off('reorderZForArrayItem')
    
    logger.debug(' ZeroCode')
  })
</script>

