<template>
  <div class="zero-code-modal-overlay" @click="$emit('close')">
    <div class="zero-code-modal-content" @click.stop>
      <div class="zero-code-modal-header">
        <h4>{{ category === 'common' ? '' : '' }}</h4>
        <button class="zero-code-img-close-btn" @click="$emit('close')">&times;</button>
      </div>

      <div class="zero-code-modal-body">
        <div class="zero-code-img-file-input-section">
          <label for="file-input" class="zero-code-img-file-input-label">
            <div class="zero-code-img-file-input-area">
              <span class="zero-code-img-file-input-text"></span>
              <span class="zero-code-img-file-input-hint">&</span>
            </div>
          </label>
          <input
            id="file-input"
            type="file"
            accept="image/*"
            style="display: none"
            @change="onFileChange"
            @drop="onFileDrop"
            @dragover.prevent
            @dragenter.prevent
          >
        </div>

        <div v-if="selectedFile" class="zero-code-img-file-info">
          <div class="zero-code-img-file-preview">
            <img :src="previewUrl" alt="">
          </div>
          <div class="zero-code-img-file-details">
            <p><strong>:</strong> {{ selectedFile.name }}</p>
            <p><strong>:</strong> {{ formatFileSize(selectedFile.size) }}</p>
            <p><strong>:</strong> {{ selectedFile.type }}</p>
          </div>
        </div>

        <div class="zero-code-form-section">
          <div class="zero-code-form-group">
            <label for="image-name">:</label>
            <input
              id="image-name"
              v-model="imageName"
              type="text"
              placeholder=""
              :disabled="!selectedFile"
            >
          </div>

          <div class="zero-code-form-group">
            <label for="image-alt">alt:</label>
            <input
              id="image-alt"
              v-model="imageAlt"
              type="text"
              placeholder=""
              :disabled="!selectedFile"
            >
          </div>

          <div v-if="category === 'individual'" class="zero-code-form-group">
            <label for="component-id">ID:</label>
            <input
              id="component-id"
              v-model="componentId"
              type="text"
              placeholder="ID"
              :disabled="!selectedFile"
            >
          </div>
        </div>
      </div>

      <div class="zero-code-modal-footer">
        <button class="zero-code-img-btn zero-code-img-btn-secondary" @click="$emit('close')"></button>
        <button class="zero-code-img-btn zero-code-img-btn-primary" :disabled="!canUpload" @click="upload">
          
        </button>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
  import { ref, computed } from 'vue'
  import type { ImageData } from '@types'

  // Props
  interface Props {
    category: 'common' | 'individual'
    componentId?: string
  }

  const props = defineProps<Props>()

  // Emits
  const emit = defineEmits<{
    close: []
    upload: [imageData: ImageData]
  }>()

  // 
  const selectedFile = ref<File | null>(null)
  const previewUrl = ref<string>('')
  const imageName = ref<string>('')
  const imageAlt = ref<string>('')
  const componentId = ref<string>(props.componentId || '')

  // 
  const canUpload = computed(() => {
    return selectedFile.value && imageName.value.trim() && imageAlt.value.trim()
  })

  // 
  const onFileChange = (event: Event) => {
    const target = event.target as HTMLInputElement
    if (target.files && target.files[0]) {
      handleFileSelect(target.files[0])
    }
  }

  // &
  const onFileDrop = (event: DragEvent) => {
    event.preventDefault()
    if (event.dataTransfer && event.dataTransfer.files[0]) {
      handleFileSelect(event.dataTransfer.files[0])
    }
  }

  // 
  const handleFileSelect = (file: File) => {
    selectedFile.value = file
    imageName.value = file.name.replace(/\.[^/.]+$/, '') // 
    imageAlt.value = file.name

    // URL
    if (previewUrl.value) {
      URL.revokeObjectURL(previewUrl.value)
    }
    previewUrl.value = URL.createObjectURL(file)
  }

  // Base64
  const fileToBase64 = (file: File): Promise<string> => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader()
      reader.readAsDataURL(file)
      reader.onload = () => resolve(reader.result as string)
      reader.onerror = error => reject(error)
    })
  }

  // ID
  const generateUniqueId = (): string => {
    return Date.now().toString(36) + Math.random().toString(36).substr(2)
  }

  // 
  const formatFileSize = (bytes: number): string => {
    if (bytes === 0) return '0 Bytes'
    const k = 1024
    const sizes = ['Bytes', 'KB', 'MB', 'GB']
    const i = Math.floor(Math.log(bytes) / Math.log(k))
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
  }

  // 
  const upload = async () => {
    if (!selectedFile.value || !canUpload.value) return

    try {
      // Base64
      const base64 = await fileToBase64(selectedFile.value)

      // Base64data:
      const base64WithoutPrefix = base64.split(',')[1] || base64

      // 
      const imageData: ImageData = {
        id: generateUniqueId(),
        name: imageName.value,
        originalName: selectedFile.value.name,
        url: `/img/${imageName.value}`, // URL
        alt: imageAlt.value,
        size: selectedFile.value.size,
        type: selectedFile.value.type,
        uploadedAt: new Date().toISOString(),
        category: props.category,
        componentId: props.category === 'individual' ? componentId.value : undefined,
        fileData: {
          base64: base64WithoutPrefix, // data:Base64
        },
      }

      emit('upload', imageData)
    } catch (error) {
      alert('')
    }
  }

  // 
  const cleanup = () => {
    if (previewUrl.value) {
      URL.revokeObjectURL(previewUrl.value)
    }
  }

  // 
  import { onUnmounted } from 'vue'
  onUnmounted(cleanup)
</script>
