import { zForZSlotParts, zForZSlotPages } from './zForZSlotTestData'
import type { SampleDataSet } from '../types'

export const testDataSet: SampleDataSet = {
  parts: {
    common: zForZSlotParts.filter(section => 
      section.type === 'parts' || section.module.some(m => m.category === 'common')
    ),
    individual: zForZSlotParts.filter(section => 
      section.type !== 'parts' && !section.module.some(m => m.category === 'common')
    ),
  },
  pages: zForZSlotPages,
  images: {
    common: [],
    individual: [],
  },
}

