<template>
  <Teleport :to="safeModalContainer">
    <div v-if="showEditor && selectedComponent" class="zero-code-overlay" @click="closeEditor">
      <div class="zero-code-sidebar" @click.stop>
        <div class="zero-code-header">
          <div class="zero-code-header-buttons">
            <button class="zero-code-btn zero-code-close-button" @click="closeEditor"></button>
          </div>
        </div>

        <div class="zero-code-content-sidebar">
          <div v-if="selectedComponent" class="zero-code-editable-component">
            <div class="zero-code-component-info">
              <span class="zero-code-component-type">{{ selectedComponent.type }}</span>
              <span class="zero-code-component-module">{{ selectedComponent.module_name }}</span>
              <span class="zero-code-component-id">ID: {{ selectedComponent.id }}</span>
            </div>

            <div class="zero-code-fields">
              <!--  -->
              <div
                v-for="field in getFieldInfo(selectedComponent)"
                :key="field.propKey"
                class="zero-code-template-field"
              >
                <label class="zero-code-field-label">{{ field.label }}:</label>

                <!--  -->
                <div v-if="field.type === 'text'" class="zero-code-text-group">
                  <input
                    :value="selectedComponent[field.propKey] || field.defaultValue"
                    type="text"
                    class="zero-code-field-input"
                    @input="
                      (e: Event) => {
                        const target = e.target as HTMLInputElement
                        updateValue(field.propKey, target.value)
                      }
                    "
                  >
                </div>

                <!--  -->
                <div v-if="field.type === 'radio'" class="zero-code-radio-group">
                  <div v-for="option in field.options" :key="option" class="zero-code-radio-item">
                    <input
                      :id="`${selectedComponent.id}_${field.propKey}_${option}`"
                      :value="option"
                      type="radio"
                      :name="`${selectedComponent.id}_${field.propKey}`"
                      class="zero-code-radio-input"
                      :checked="
                        selectedComponent[field.propKey] === option ||
                          (option === '' && !selectedComponent[field.propKey])
                      "
                      @change="updateValue(field.propKey, option === '' ? '' : option)"
                    >
                    <label
                      :for="`${selectedComponent.id}_${field.propKey}_${option}`"
                      class="zero-code-radio-label"
                    >
                      {{ option }}
                    </label>
                  </div>
                </div>

                <!--  -->
                <div v-if="field.type === 'checkbox'" class="zero-code-checkbox-group">
                  <div v-for="option in field.options" :key="option" class="zero-code-checkbox-item">
                    <input
                      :id="`${selectedComponent.id}_${field.propKey}_${option}`"
                      :checked="
                        isCheckboxChecked(selectedComponent, field.propKey, option) ||
                          (Array.isArray(field.defaultValue) &&
                            field.defaultValue.includes(option) &&
                            !selectedComponent[field.propKey])
                      "
                      type="checkbox"
                      class="zero-code-checkbox-input"
                      @change="
                        (e: Event) => {
                          const target = e.target as HTMLInputElement
                          updateCheckboxValue(field.propKey, option, target.checked)
                        }
                      "
                    >
                    <label
                      :for="`${selectedComponent.id}_${field.propKey}_${option}`"
                      class="zero-code-checkbox-label"
                    >
                      {{ option }}
                    </label>
                  </div>
                </div>

                <!--  -->
                <div v-if="field.type === 'select'" class="zero-code-select-group">
                  <select
                    :value="selectedComponent[field.propKey] || field.defaultValue"
                    class="zero-code-field-input zero-code-select-input"
                    @change="
                      (e: Event) => {
                        const target = e.target as HTMLSelectElement
                        updateValue(field.propKey, target.value)
                      }
                    "
                  >
                    <option v-for="option in field.options" :key="option" :value="option">
                      {{ option }}
                    </option>
                  </select>
                </div>

                <!--  -->
                <div v-if="field.type === 'select-multiple'" class="zero-code-select-multiple-group">
                  <select
                    :value="selectedComponent[field.propKey] || field.defaultValue"
                    multiple
                    class="zero-code-field-input zero-code-select-multiple"
                    @change="
                      (e: Event) => {
                        const target = e.target as HTMLSelectElement
                        const selectedValues = Array.from(target.selectedOptions).map(
                          option => option.value
                        )
                        updateValue(field.propKey, selectedValues)
                      }
                    "
                  >
                    <option
                      v-for="option in field.options"
                      :key="option"
                      :value="option"
                      :selected="
                        Array.isArray((selectedComponent as any)[field.propKey]) &&
                          (selectedComponent as any)[field.propKey].includes(option)
                      "
                    >
                      {{ option }}
                    </option>
                  </select>
                </div>
              </div>

              <!--  -->
              <template v-for="(value, key) in selectedComponent" :key="String(key)">
                <div
                  v-if="shouldShowBasicField(String(key), value, selectedComponent)"
                  class="zero-code-field-group"
                >
                  <label :for="`${selectedComponent.id}_${String(key)}`">{{ key }}:</label>
                  <input
                    :id="`${selectedComponent.id}_${String(key)}`"
                    :value="value"
                    :type="getInputType(String(key))"
                    class="zero-code-field-input"
                    :readonly="String(key) === 'id'"
                    @input="
                      (e: Event) => {
                        const target = e.target as HTMLInputElement
                        updateValue(String(key), target.value)
                      }
                    "
                  >
                </div>
              </template>

              <!--  -->
              <template v-for="(value, key) in selectedComponent" :key="String(key)">
                <ArrayFieldEditor
                  v-if="shouldShowArrayField(String(key), value, selectedComponent)"
                  :component-id="selectedComponent.id"
                  :array-key="String(key)"
                  :items="Array.isArray(value) ? (value as Record<string, unknown>[]) : []"
                  :fields="getArrayFields(String(key))"
                  :label="getArrayLabel(String(key))"
                  :item-label="getArrayItemLabel(String(key))"
                  @add="() => addArrayItem(selectedComponent, String(key))"
                  @remove="index => removeArrayItem(selectedComponent, String(key), index)"
                  @reorder="
                    (fromIndex, toIndex) => reorderArrayItems(String(key), fromIndex, toIndex)
                  "
                  @update="
                    (index, fieldKey, fieldValue) =>
                      updateArrayItemValue(String(key), index, fieldKey, fieldValue)
                  "
                />
              </template>
            </div>
          </div>
        </div>
      </div>
    </div>
  </Teleport>
</template>

<script setup lang="ts">
  import { watch, computed } from 'vue'
  import { logger } from '@shared/utils/logger'
  import ArrayFieldEditor from './ArrayFieldEditor.vue'
  import type { Component } from '@types'
  import { useComponentUtils } from '../../composables/useComponentUtils'

  // ArrayFieldEditorField
  interface LocalField {
    label: string
    type: 'text' | 'select' | 'textarea' | 'number' | 'radio' | 'checkbox' | 'select-multiple'
    options?: string[]
    defaultValue?: any
  }

  interface Props {
    showEditor: boolean
    selectedComponent: Component | null
    getFieldInfo: (component: Component) => any[]
    shouldShowBasicField: (key: string, value: unknown, component: Component) => boolean
    shouldShowArrayField: (key: string, value: unknown, component: Component) => boolean
    shouldShowArrayItemField: (
      itemKey: string,
      itemValue: unknown,
      component: Component,
      arrayKey?: string,
      item?: unknown
    ) => boolean
    getInputType: (key: string) => string
    isCheckboxChecked: (component: Component, propKey: string, option: string) => boolean
    partsData?: any[]
    modalContainer?: string
  }

  const props = withDefaults(defineProps<Props>(), {
    modalContainer: 'body'
  })

  // 
  const isModalContainerValid = computed(() => {
    if (!props.modalContainer || props.modalContainer === 'body') {
      return true // body
    }
    
    // DOMTeleport
    return typeof document !== 'undefined'
  })

  // 
  const safeModalContainer = computed(() => {
    if (!props.modalContainer || props.modalContainer === 'body') {
      return 'body'
    }
    
    // DOMclient-side
    if (typeof document !== 'undefined') {
      const container = document.querySelector(props.modalContainer)
      return container ? props.modalContainer : 'body'
    }
    
    return 'body'
  })

  const emit = defineEmits<{
    (e: 'close'): void
    (e: 'update:value', key: string, value: unknown): void
    (e: 'update:array-item', key: string, index: number, value: unknown): void
    (e: 'update:checkbox', key: string, option: string, checked: boolean): void
    (e: 'add:array-item', key: string): void
    (e: 'remove:array-item', key: string, index: number): void
    (e: 'reorder:array-item', key: string, fromIndex: number, toIndex: number): void
  }>()

  const closeEditor = (): void => {
    emit('close')
  }

  const updateValue = (key: string, value: unknown): void => {
    emit('update:value', key, value)
  }


  // 
  const ensureDefaultValues = (): void => {
    if (!props.selectedComponent) return

    const fieldInfos = props.getFieldInfo(props.selectedComponent)

    fieldInfos.forEach(field => {
      // 
      if (field.defaultValue && !props.selectedComponent![field.propKey]) {
        // 
        if (
          (field.type === 'checkbox' || field.type === 'select-multiple') &&
          Array.isArray(field.defaultValue)
        ) {
          updateValue(field.propKey, [...field.defaultValue])
        } else {
          updateValue(field.propKey, field.defaultValue)
        }
      }
    })
  }

  // 
  watch(
    () => props.selectedComponent,
    newComponent => {
      if (newComponent) {
        ensureDefaultValues()
      }
    },
    { immediate: true }
  )

  const updateArrayItemValue = (
    key: string,
    index: number,
    itemKey: string,
    value: unknown
  ): void => {
    console.log(` [EditorPanel] updateArrayItemValue:`, { 
      key, 
      index, 
      itemKey, 
      value,
      componentId: props.selectedComponent?.id,
      timestamp: new Date().toISOString()
    })
    
    if (!props.selectedComponent) {
      console.error(' [EditorPanel] selectedComponent')
      return
    }
    
    const arrayItems = props.selectedComponent[key]
    if (!Array.isArray(arrayItems)) {
      console.error(` [EditorPanel] ${key}:`, arrayItems)
      return
    }
    
    console.log(` [EditorPanel] :`, arrayItems[index])
    
    const item = { ...arrayItems[index], [itemKey]: value }
    
    console.log(` [EditorPanel] :`, item)
    console.log(` [EditorPanel] update:array-item:`, { key, index, item })
    
    emit('update:array-item', key, index, item)
  }

  const updateCheckboxValue = (key: string, option: string, checked: boolean): void => {
    emit('update:checkbox', key, option, checked)
  }

  const addArrayItem = (component: Component | null, key: string): void => {
    if (!component) return

    // EditorPanel
    emit('add:array-item', key)
  }

  const removeArrayItem = (component: Component | null, key: string, index: number): void => {
    if (!component) return

    // EditorPanel
    emit('remove:array-item', key, index)
  }

  const reorderArrayItems = (key: string, fromIndex: number, toIndex: number): void => {
    if (!props.selectedComponent) return

    const arrayItems = props.selectedComponent[key] as any[]
    if (!Array.isArray(arrayItems)) return

    // 
    const newArray = [...arrayItems]
    const [movedItem] = newArray.splice(fromIndex, 1)
    newArray.splice(toIndex, 0, movedItem)

    // 
    ;(props.selectedComponent as any)[key] = newArray

    logger.debug(` : ${key}[${fromIndex}]  [${toIndex}]`)

    // 
    emit('reorder:array-item', key, fromIndex, toIndex)
  }

  // 
  const getArrayFields = (key: string): Record<string, LocalField> => {
    if (!props.selectedComponent) return {}

    const fields: Record<string, LocalField> = {}

    // 
    if (props.partsData) {
      const utils = useComponentUtils(props.partsData)
      const templateFields = utils.getArrayItemFieldInfo(props.selectedComponent, key)

      // 
      templateFields.forEach(fieldInfo => {
        fields[fieldInfo.propKey] = {
          label: fieldInfo.label,
          type: fieldInfo.type as any,
          options: fieldInfo.options as string[],
          defaultValue: fieldInfo.defaultValue,
        }
      })

      // 
      if (templateFields.length > 0) {
        return fields
      }
    }

    // 
    const arrayValue = props.selectedComponent[key] as unknown
    if (Array.isArray(arrayValue) && arrayValue.length > 0) {
      const firstItem = arrayValue[0]
      if (typeof firstItem === 'object' && firstItem !== null) {
        Object.keys(firstItem).forEach(fieldKey => {
          if (fieldKey === 'id') return // ID

          const fieldValue = firstItem[fieldKey]

          // 
          fields[fieldKey] = {
            label: fieldKey.replace(/_/g, ' ').replace(/\b\w/g, l => l.toUpperCase()),
            type: fieldKey.includes('desc') || fieldKey.includes('content') ? 'textarea' : 'text',
          }
        })
      }
    }

    // 
    if (Object.keys(fields).length === 0) {
      return {
        title: {
          label: '',
          type: 'text',
        },
        content: {
          label: '',
          type: 'textarea',
        },
      }
    }

    return fields
  }

  // 
  const getArrayLabel = (key: string): string => {
    // 
    return key
      .replace(/_/g, ' ')
      .replace(/\b\w/g, l => l.toUpperCase())
      .concat(' ')
  }

  // 
  const getArrayItemLabel = (key: string): string => {
    // 
    let itemName = key
    if (key.endsWith('s')) {
      itemName = key.slice(0, -1)
    }
    return itemName.replace(/_/g, ' ').replace(/\b\w/g, l => l.toUpperCase())
  }
</script>

