import type { Component, ArrayItem } from '@types'
import { logger } from '@shared/utils/logger'
import { errorHandler, safeExecute, ErrorType } from '@shared/utils/errorHandler'
import { decodeHTMLEntities } from '@shared/utils/htmlHelpers'


/**
 * 
 * 41
 */
export function processTemplateNotationOptimized(
  html: string,
  component: Component,
  template: string
): string {
  return (
    safeExecute(
      () => {
        logger.debug(``, {
          componentId: component.id,
          htmlLength: html.length,
        })

        let processedHtml = html

        // 1
        processedHtml = processedHtml.replace(
          /(\(\$[^:)]+\?:[^)]*\)|\(\$[^:)]+:[^)]*\)|\{\$[^:}]+:[^}]*\})/g,
          match => {
            //  ($param?:option1|option2|)
            if (match.includes('?:')) {
              return processConditionalMatch(match, component)
            }

            //  ($param:option1|option2|option3)
            if (match.startsWith('(') && match.includes(':')) {
              return processSelectionMatch(match, component)
            }

            //  {$param:}
            if (match.startsWith('{') && match.includes(':')) {
              return processTextReplacementMatch(match, component, template)
            }

            return match
          }
        )

        logger.success(``, {
          componentId: component.id,
          resultLength: processedHtml.length,
        })

        return processedHtml
      },
      errorHandler,
      ` (componentId: ${component.id})`,
      ErrorType.TEMPLATE
    ) || html
  )
}

/**
 * 
 */
function processConditionalMatch(match: string, component: Component): string {
  const conditionalMatch = match.match(/\(\$([^:?]+)\?:([^)]*)\)/)
  if (!conditionalMatch) return match

  const [, paramName, optionsStr] = conditionalMatch
  const options = optionsStr.split('|')
  const value = component[paramName]

  if (Array.isArray(value)) {
    return value.join(' ')
  } else if (value && options.includes(String(value))) {
    return String(value)
  }

  return options[0] || ''
}

/**
 * 
 */
function processSelectionMatch(match: string, component: Component): string {
  const selectionMatch = match.match(/\(\$([^:]+):([^)]*)\)/)
  if (!selectionMatch) return match

  const [, paramName, optionsStr] = selectionMatch
  const options = optionsStr.split('|')
  const value = component[paramName]

  if (value && options.includes(String(value))) {
    return String(value)
  }

  return options[0] || ''
}

/**
 * 
 */
function processTextReplacementMatch(
  match: string,
  component: Component,
  template: string
): string {
  const textMatch = match.match(/\{\$([^:]+):([^}]*)\}/)
  if (!textMatch) return match

  const [, paramName, defaultText] = textMatch
  const rawValue = component[paramName]
  
  // HTML
  const value = rawValue ? decodeHTMLEntities(String(rawValue)) : decodeHTMLEntities(defaultText)

  // Tiptap
  if (shouldApplyTiptapOptimized(template, paramName)) {
    return `<div class="tiptap-content" data-component-id="${component.id}" data-param-name="${paramName}">${value}</div>`
  }

  return value
}

/**
 * Tiptap
 */
function shouldApplyTiptapOptimized(template: string, paramName: string): boolean {
  // 
  const escapedParamName = paramName.replace(/[.*+?^${}()|[\]\\]/g, '\\$&')
  const paramPattern = new RegExp(
    `\\{\\$${escapedParamName}:[^}]*\\}`,
    'g'
  )
  const escapedParamPattern = new RegExp(
    `\\{\\$${escapedParamName}:[^}]*\\}`.replace(/\{/g, '&lt;').replace(/\}/g, '&gt;'),
    'g'
  )
  
  let match
  while ((match = paramPattern.exec(template)) !== null) {
    const matchStart = match.index
    
    // 
    const beforeMatch = template.substring(0, matchStart)
    const lastOpenBracket = beforeMatch.lastIndexOf('<')
    const lastCloseBracket = beforeMatch.lastIndexOf('>')
    
    //  '<'  '>' 
    if (lastOpenBracket > lastCloseBracket) {
      // 
      continue
    }
    
    // 
    const codeBlockRanges: Array<{ start: number; end: number }> = []
    
    // pre, code 
    const preCodePattern = /<(pre|code)[^>]*>[\s\S]*?<\/(pre|code)>/gi
    // code-block  div
    const codeBlockDivPattern = /<div[^>]*class="[^"]*code-block[^"]*"[^>]*>[\s\S]*?<\/div>/gi
    
    const patterns = [preCodePattern, codeBlockDivPattern]
    
    patterns.forEach(pattern => {
      pattern.lastIndex = 0
      let match: RegExpExecArray | null
      while ((match = pattern.exec(template)) !== null) {
        codeBlockRanges.push({
          start: match.index,
          end: match.index + match[0].length
        })
      }
    })
    
    // 
    const isInCodeBlock = codeBlockRanges.some(range => 
      matchStart >= range.start && matchStart <= range.end
    )
    
    if (isInCodeBlock) {
      continue
    }
    
    // 
    return true
  }

  return false
}

/**
 * 
 */
const templateCache = new Map<string, string>()

export function getTemplateOptimized(
  type: string,
  moduleName: string,
  getTemplate: (type: string, moduleName: string) => string
): string {
  const key = `${type}:${moduleName}`

  if (templateCache.has(key)) {
    return templateCache.get(key)!
  }

  const template = getTemplate(type, moduleName)
  templateCache.set(key, template)

  return template
}

/**
 * 
 */
export function clearTemplateCache(): void {
  templateCache.clear()
  logger.debug('')
}

/**
 * 
 */
export function processArrayItemOptimized(
  itemHtml: string,
  item: ArrayItem,
  component: Component,
  template: string,
  arrayName: string
): string {
  return (
    safeExecute(
      () => {
        let processedHtml = itemHtml

        // 
        Object.keys(item).forEach(key => {
          if (key !== 'id') {
            const value = item[key]

            if (typeof value === 'string') {
              // HTML
              const decodedValue = decodeHTMLEntities(value)
              
              // 
              const patterns = [
                { regex: new RegExp(`\\{\\$${key}:([^}]+)\\}`, 'g'), replacement: decodedValue },
                { regex: new RegExp(`\\(\\$${key}:([^)]+)\\)`, 'g'), replacement: decodedValue },
              ]

              patterns.forEach(({ regex, replacement }) => {
                processedHtml = processedHtml.replace(regex, replacement)
              })
            } else if (Array.isArray(value)) {
              // 
              const regex = new RegExp(`\\(\\$${key}\\?:([^)]+)\\)`, 'g')
              processedHtml = processedHtml.replace(regex, value.join(' '))
            }
          }
        })

        return processedHtml
      },
      errorHandler,
      ``,
      ErrorType.TEMPLATE
    ) || itemHtml
  )
}
