// 
export const TEMPLATE_PATTERNS = {
  TEXT_REPLACEMENT: /\{\$([^:]+):(.*?)\}/g,
  SELECTION_SYNTAX: /\(\$([^:?]+):(.*?)\)/g,
  CONDITIONAL_SYNTAX: /\(\$([^:?]+)\?:(.*?)\)/g,
  Z_FOR_PATTERN: (arrayName: string) => new RegExp(`z-for="${arrayName}"`),
  Z_FOR_ELEMENT: /<(\w+)([^>]*z-for="([^"]*)"[^>]*)>([\s\S]*?)<\/\1>/g,
  Z_FOR_ELEMENT_START: (arrayName: string) => new RegExp(`<(\\w+)[^>]*z-for="${arrayName}"[^>]*>`),
  Z_FOR_SELF_CLOSING: /<(\w+)([^>]*z-for="([^"]*)"[^>]*)\s*\/>/g,
} as const

// 
export const ERROR_CODES = {
  COMPONENT_NOT_FOUND: 'COMPONENT_NOT_FOUND',
  TEMPLATE_NOT_FOUND: 'TEMPLATE_NOT_FOUND',
  INVALID_ARRAY_INDEX: 'INVALID_ARRAY_INDEX',
  Z_FOR_ITEM_NOT_FOUND: 'Z_FOR_ITEM_NOT_FOUND',
  TEMPLATE_PARSE_ERROR: 'TEMPLATE_PARSE_ERROR',
  DRAG_DROP_ERROR: 'DRAG_DROP_ERROR',
} as const

// 
export type SystemField = (typeof SYSTEM_FIELDS)[number]
export type ZForSystemField = (typeof Z_FOR_SYSTEM_FIELDS)[number]
export type ErrorCode = (typeof ERROR_CODES)[keyof typeof ERROR_CODES]
