import { ref, computed, type Ref, type ComputedRef } from 'vue'
import { logger } from '@shared/utils/logger'
import { ErrorType } from '@shared/utils/errorHandler'

// ErrorType
export { ErrorType }

// 
export interface ErrorInfo {
  id: string
  type: ErrorType
  message: string
  details?: string
  timestamp: Date
  context?: Record<string, unknown>
}

// 
export interface ErrorHandlerReturn {
  errors: Ref<ErrorInfo[]>
  hasErrors: ComputedRef<boolean>
  handleError: (error: Error | string, type?: ErrorType, context?: Record<string, unknown>) => void
  clearErrors: () => void
  clearError: (id: string) => void
  getErrorsByType: (type: ErrorType) => ErrorInfo[]
  showErrorNotification: (message: string, type?: ErrorType) => void
}

// Composable
export function useErrorHandler(): ErrorHandlerReturn {
  // 
  const errors = ref<ErrorInfo[]>([])

  // 
  const hasErrors = computed(() => errors.value.length > 0)

  // 
  const handleError = (
    error: Error | string,
    type: ErrorType = ErrorType.UNKNOWN,
    context?: Record<string, unknown>
  ): void => {
    const errorInfo: ErrorInfo = {
      id: `error_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`,
      type,
      message: error instanceof Error ? error.message : error,
      details: error instanceof Error ? error.stack : undefined,
      timestamp: new Date(),
      context,
    }

    errors.value.push(errorInfo)

    // 
    if (import.meta.env.DEV) {
      logger.error(`[${type}] ${errorInfo.message}`, {
        details: errorInfo.details,
        context: errorInfo.context,
      })
    }

    // 10
    setTimeout(() => {
      clearError(errorInfo.id)
    }, 10000)
  }

  // 
  const clearErrors = (): void => {
    errors.value = []
  }

  // 
  const clearError = (id: string): void => {
    const index = errors.value.findIndex(error => error.id === id)
    if (index !== -1) {
      errors.value.splice(index, 1)
    }
  }

  // 
  const getErrorsByType = (type: ErrorType): ErrorInfo[] => {
    return errors.value.filter(error => error.type === type)
  }

  // 
  const showErrorNotification = (message: string, type: ErrorType = ErrorType.UNKNOWN): void => {
    handleError(message, type)
  }

  return {
    errors,
    hasErrors,
    handleError,
    clearErrors,
    clearError,
    getErrorsByType,
    showErrorNotification,
  }
}

// 
export function setupGlobalErrorHandler(): void {
  // Vue 
  window.addEventListener('error', event => {
    const errorHandler = useErrorHandler()
    errorHandler.handleError(event.error || event.message, ErrorType.COMPONENT, {
      filename: event.filename,
      lineno: event.lineno,
      colno: event.colno,
    })
  })

  // Promise  reject 
  window.addEventListener('unhandledrejection', event => {
    const errorHandler = useErrorHandler()
    errorHandler.handleError(event.reason, ErrorType.NETWORK, {
      promise: event.promise,
    })
  })
}

// Composable
export function useErrorBoundary() {
  const errorHandler = useErrorHandler()

  // 
  const catchError = <T>(
    asyncFn: () => Promise<T>,
    errorType: ErrorType = ErrorType.UNKNOWN_ERROR,
    context?: Record<string, unknown>
  ): Promise<T | null> => {
    return asyncFn().catch(error => {
      errorHandler.handleError(error, errorType, context)
      return null
    })
  }

  // 
  const tryCatch = <T>(
    fn: () => T,
    errorType: ErrorType = ErrorType.UNKNOWN_ERROR,
    context?: Record<string, unknown>
  ): T | null => {
    try {
      return fn()
    } catch (error) {
      errorHandler.handleError(error as Error, errorType, context)
      return null
    }
  }

  return {
    ...errorHandler,
    catchError,
    tryCatch,
  }
}
