/**
 * DOM
 * DOMParser
 */

import type { Component } from '@types'
import { logger } from '@shared/utils/logger'
import { errorHandler, safeExecute, ErrorType } from '@shared/utils/errorHandler'
import { escapeJSString } from '@shared/utils/htmlHelpers'


/**
 * DOMParser
 */
const sharedDOMParser = new DOMParser()

/**
 * HTML
 */
const parseCache = new Map<string, Document>()
const maxCacheSize = 50 // 

/**
 * HTML
 */
export function parseHTMLOptimized(html: string): Document {
  return (
    safeExecute(
      () => {
        // 
        if (parseCache.has(html)) {
          const cached = parseCache.get(html)
          if (cached) {
            logger.debug('DOMParser: ')
            return cached.cloneNode(true) as Document
          }
        }

        // 
        const doc = sharedDOMParser.parseFromString(html, 'text/html')

        // 
        if (parseCache.size >= maxCacheSize) {
          // FIFO
          const firstKey = parseCache.keys().next().value
          if (firstKey) {
            parseCache.delete(firstKey)
          }
        }

        // 
        parseCache.set(html, doc.cloneNode(true) as Document)
        logger.debug('DOMParser: ')

        return doc
      },
      errorHandler,
      'HTML',
      ErrorType.TEMPLATE
    ) || sharedDOMParser.parseFromString('<div></div>', 'text/html')
  )
}

/**
 * DOMParser
 */
export function findElementsWithAttribute(
  html: string,
  attributeName: string
): Array<{
  fullMatch: string
  attributeValue: string
  innerContent: string
}> {
  return (
    safeExecute(
      () => {
        const results: Array<{ fullMatch: string; attributeValue: string; innerContent: string }> =
          []

        // 
        const pattern = new RegExp(
          `<([^>]*${attributeName}="([^"]*)"[^>]*)>([\\s\\S]*?)</\\1>`,
          'g'
        )
        let match

        while ((match = pattern.exec(html)) !== null) {
          results.push({
            fullMatch: match[0],
            attributeValue: match[2],
            innerContent: match[3],
          })
        }

        logger.debug(`: ${attributeName}${results.length}`)
        return results
      },
      errorHandler,
      ` (attribute: ${attributeName})`,
      ErrorType.TEMPLATE
    ) || []
  )
}

/**
 * z-directive
 */
export function processZDirectivesOptimized(
  html: string,
  component: Component,
  getTemplate: (type: string, moduleName: string) => string,
  currentDepth: number = 1
): string {
  return (
    safeExecute(
      () => {
        logger.debug(`z-directive`, {
          componentId: component.id,
          currentDepth,
        })

        let processedHtml = html

        // z-if
        processedHtml = processZIfOptimized(processedHtml, component)

        // z-for
        processedHtml = processZForOptimized(processedHtml, component, getTemplate)

        // z-slotDOMParser
        processedHtml = processZSlotOptimized(processedHtml, component, currentDepth, getTemplate)

        logger.success(`z-directive`, {
          componentId: component.id,
          resultLength: processedHtml.length,
        })

        return processedHtml
      },
      errorHandler,
      `z-directive (componentId: ${component.id})`,
      ErrorType.TEMPLATE
    ) || html
  )
}

/**
 * z-if
 */
function processZIfOptimized(html: string, component: Component): string {
  return html.replace(
    /<([^>]*z-if="([^"]*)"[^>]*)>([\\s\\S]*?)<\/[^>]*>/g,
    (match, tagAttributes, condition, innerContent) => {
      const shouldShow = component[condition]

      if (!shouldShow) {
        logger.debug(`z-if: ${condition}`)
        return '' // 
      }

      // z-if
      const cleanAttributes = tagAttributes.replace(/\s*z-if="[^"]*"\s*/, ' ').trim()
      logger.debug(`z-if: ${condition}`)

      return `<div ${cleanAttributes}>${innerContent}</div>`
    }
  )
}

/**
 * z-for
 */
function processZForOptimized(
  html: string,
  component: Component,
  getTemplate: (type: string, moduleName: string) => string
): string {
  return html.replace(
    /<(\w+)([^>]*z-for="([^"]*)"[^>]*)>([\s\S]*?)<\/\1>/g,
    (match, tagName, attributes, arrayName, innerContent) => {
      const arrayData = component[arrayName]
      logger.debug(`z-for: ${arrayName}`, {
        length: Array.isArray(arrayData) ? arrayData.length : 'not array',
      })

      if (!Array.isArray(arrayData) || arrayData.length === 0) {
        // 
        return `
          <div class="add-array-item-button-container">
            <button class="add-array-item-button" onclick="window.addArrayItem('${component.id}', '${arrayName}')">
              <span class="zero-code-plus-icon"></span>
              <span class="zero-code-button-text">${arrayName}</span>
            </button>
          </div>
        `
      }

      // 
      const renderedItems = arrayData
        .map((item: Record<string, unknown>) => {
          let itemHtml = innerContent

          // 
          Object.keys(item).forEach(key => {
            if (key !== 'id') {
              const value = item[key]
              if (typeof value === 'string') {
                const patterns = [
                  new RegExp(`\\{\\$${key}:([^}]+)\\}`, 'g'),
                  new RegExp(`\\(\\$${key}:([^)]+)\\)`, 'g'),
                ]
                patterns.forEach(pattern => {
                  itemHtml = itemHtml.replace(pattern, value)
                })
              } else if (Array.isArray(value)) {
                const pattern = new RegExp(`\\(\\$${key}\\?:([^)]+)\\)`, 'g')
                itemHtml = itemHtml.replace(pattern, value.join(' '))
              }
            }
          })

          return itemHtml
        })
        .join('')

      return renderedItems
    }
  )
}

/**
 * z-slotDOMParser
 */
function processZSlotOptimized(
  html: string,
  component: Component,
  currentDepth: number,
  getTemplate: (type: string, moduleName: string) => string
): string {
  const doc = parseHTMLOptimized(html)
  const slotElements = doc.querySelectorAll('[z-slot]')

  logger.debug(`z-slot: ${slotElements.length}`)

  slotElements.forEach((element: Element) => {
    const slotName = element.getAttribute('z-slot')

    if (slotName) {
      const nestedParts = component[slotName]
      const components = Array.isArray(nestedParts) ? nestedParts : []

      if (components.length === 0) {
        // 
        // 
        const existingContent = element.innerHTML
        element.innerHTML = existingContent + `
          <div class="zero-code-add-nested-part-button-container">
            <button class="zero-code-add-nested-part-button" onclick="window.openNestedPartSelector('${escapeJSString(component.id)}', 0)">
              <span class="zero-code-plus-icon"></span>
              <span class="zero-code-button-text"></span>
            </button>
          </div>
        `
      } else {
        // UI
        const nestedHtml = components
          .map((child: Component, index: number) => {
            const depth = currentDepth + 1
            const indentLevel = Math.max(0, depth - 1) * 20

            return `
            <div class="nested-component nested-depth-${depth}" style="margin-left: ${indentLevel}px;">
              <div class="component-header">
                <span class="component-type">${child.type}</span>
                <span class="component-module">${child.module_name}</span>
                <button onclick="window.openNestedEditor('${child.id}')"></button>
                <button onclick="window.deleteNestedPart('${child.id}')"></button>
              </div>
            </div>
          `
          })
          .join('')

        // HTML
        const existingContent = element.innerHTML
        element.innerHTML = existingContent + nestedHtml
      }

      element.removeAttribute('z-slot')
    }
  })

  return doc.body.innerHTML
}

/**
 * 
 */
export function clearDOMCache(): void {
  parseCache.clear()
  logger.debug('DOM')
}

/**
 * 
 */
export function getDOMCacheStats(): {
  cacheSize: number
  maxSize: number
  hitRate: number
} {
  return {
    cacheSize: parseCache.size,
    maxSize: maxCacheSize,
    hitRate: parseCache.size > 0 ? 1 : 0, // 
  }
}
