<template>
  <div class="zero-code-tab-content">
    <!--  -->
    <div class="zero-code-data-tabs">
      <div class="zero-code-tab-buttons">
        <button
          v-for="tab in partsTabs"
          :key="tab"
          :class="['zero-code-data-tab-button', { active: activePartsTab === tab }]"
          @click="activePartsTab = tab"
        >
          {{ getPartsTabLabel(tab) }}
        </button>
      </div>
    </div>

    <div class="zero-code-parts-content">
      <!--  -->
      <div v-if="activePartsTab === 'common'" class="zero-code-parts-section">
        <div class="zero-code-section-header">
          <h4> ({{ commonParts.length }})</h4>
          <button class="zero-code-add-part-btn" @click="openRegistration('common')">
             
          </button>
        </div>
        <PartsManager
          :parts="commonParts"
          :render-part-preview="renderPartPreview"
          @edit-part="editPart"
          @delete-part="deletePart"
        />
      </div>

      <!--  -->
      <div v-if="activePartsTab === 'individual'" class="zero-code-parts-section">
        <div class="zero-code-section-header">
          <h4> ({{ individualParts.length }})</h4>
          <button class="zero-code-add-part-btn" @click="openRegistration('individual')">
             
          </button>
        </div>
        <PartsManager
          :parts="individualParts"
          :render-part-preview="renderPartPreview"
          @edit-part="editPart"
          @delete-part="deletePart"
        />
      </div>

      <!--  -->
      <div v-if="activePartsTab === 'data'" class="zero-code-parts-section">
        <div class="zero-code-data-display-section">
          <!--  -->
          <div class="zero-code-parts-sub-tabs">
            <button
              :class="['zero-code-parts-sub-tab-button', { active: activeDataSubTab === 'common' }]"
              @click="activeDataSubTab = 'common'"
            >
              Parts
            </button>
            <button
              :class="['zero-code-parts-sub-tab-button', { active: activeDataSubTab === 'individual' }]"
              @click="activeDataSubTab = 'individual'"
            >
              Parts
            </button>
          </div>

          <!--  -->
          <div class="zero-code-parts-sub-content">
            <!-- Parts -->
            <div v-if="activeDataSubTab === 'common'" class="zero-code-parts-sub-section">
              <div v-if="commonParts && commonParts.length > 0">
                <pre class="zero-code-data-display-pre">{{ formatToReadableJson(commonParts) }}</pre>
              </div>
              <div v-else>
                <p></p>
              </div>
            </div>

            <!-- Parts -->
            <div v-if="activeDataSubTab === 'individual'" class="zero-code-parts-sub-section">
              <div v-if="individualParts && individualParts.length > 0">
                <pre class="zero-code-data-display-pre">{{ formatToReadableJson(individualParts) }}</pre>
              </div>
              <div v-else>
                <p></p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!--  -->
    <PartRegistrationModal
      v-if="showPartRegistration"
      :existing-types="existingPartTypes"
      :render-part-preview="renderPartPreview"
      :editing-part="editingPart"
      :is-editing="!!editingPart"
      :part-category="currentPartCategory"
      @close="closeRegistration"
      @save="savePart"
    />
  </div>
</template>

<script setup lang="ts">
  import { ref, computed } from 'vue'
  import PartsManager from '@/components/parts/PartsManager.vue'
  import PartRegistrationModal from '@/components/parts/PartRegistrationModal.vue'
  import type { PartSection } from '@types'

  type PartsTab = 'common' | 'individual' | 'data'
  type PartCategory = 'common' | 'individual'

  // 
  interface CategorizedPartSection extends PartSection {
    category?: PartCategory
  }

  // Props
  interface Props {
    localParts: PartSection[]
    localCommonParts?: PartSection[]
    localIndividualParts?: PartSection[]
    showPartRegistration: boolean
    existingPartTypes: string[]
    editingPart: any
    renderPartPreview: (part: any) => string
  }

  // Emits
  interface Emits {
    (e: 'update-parts', parts: PartSection[]): void
    (e: 'edit-part', part: any): void
    (e: 'delete-part', part: { type: string; title: string }): void
    (e: 'show-registration'): void
    (e: 'close-registration'): void
    (e: 'save-part', partData: any): void
  }

  const props = defineProps<Props>()
  const emit = defineEmits<Emits>()

  // 
  const activePartsTab = ref<PartsTab>('common')
  const currentPartCategory = ref<PartCategory>('common')
  const activeDataSubTab = ref<'common' | 'individual'>('common')

  // 
  const partsTabs: PartsTab[] = ['common', 'individual', 'data']

  // 
  const getPartsTabLabel = (tab: PartsTab): string => {
    const labels: Record<PartsTab, string> = {
      common: '',
      individual: '',
      data: '',
    }
    return labels[tab]
  }

  // 
  const commonParts = computed(() => {
    // props
    if (props.localCommonParts && props.localCommonParts.length > 0) {
      return props.localCommonParts
    }
    
    return props.localParts.map(section => ({
      ...section,
      module: section.module.filter(part => {
        const extendedPart = part as any
        return !extendedPart.category || extendedPart.category === 'common'
      })
    })).filter(section => section.module.length > 0)
  })

  // 
  const individualParts = computed(() => {
    // props
    if (props.localIndividualParts && props.localIndividualParts.length > 0) {
      return props.localIndividualParts
    }
    
    return props.localParts.map(section => ({
      ...section,
      module: section.module.filter(part => {
        const extendedPart = part as any
        return extendedPart.category === 'individual'
      })
    })).filter(section => section.module.length > 0)
  })

  // 
  const openRegistration = (category: PartCategory) => {
    currentPartCategory.value = category
    emit('show-registration')
  }

  // 
  const closeRegistration = () => {
    emit('close-registration')
  }

  // 
  const editPart = (part: any) => {
    // 
    const extendedPart = part.module as any
    currentPartCategory.value = extendedPart.category || 'common'
    emit('edit-part', part)
  }

  // 
  const deletePart = (part: { type: string; title: string }) => {
    emit('delete-part', part)
  }

  // 
  const savePart = (partData: any) => {
    // 
    const extendedPartData = {
      ...partData,
      category: currentPartCategory.value
    }
    emit('save-part', extendedPartData)
  }

  // JSON
  const formatToReadableJson = (data: unknown): string => {
    const formatValue = (obj: unknown, depth: number = 0): string => {
      const indent = '  '.repeat(depth)
      const nextIndent = '  '.repeat(depth + 1)

      if (Array.isArray(obj)) {
        if (obj.length === 0) return '[]'
        const items = obj.map(item => nextIndent + formatValue(item, depth + 1)).join(',\n')
        return `[\n${items}\n${indent}]`
      }

      if (typeof obj === 'object' && obj !== null) {
        const entries = Object.entries(obj as Record<string, unknown>)
        if (entries.length === 0) return '{}'
        
        const items = entries.map(([key, value]) => {
          let formattedValue: string
          
          if (typeof value === 'string') {
            // 
            const cleanString = value
              .replace(/\\n/g, '\n')
              .replace(/\\t/g, '\t')
              .replace(/\\"/g, '"')
              .replace(/\\'/g, "'")
              .replace(/\\\\/g, '\\')
            
            // 
            if (cleanString.includes('\n')) {
              const lines = cleanString.split('\n')
              const formattedLines = lines.map((line, index) => {
                if (index === 0) return `\`${line}`
                if (index === lines.length - 1) return `${nextIndent}${line}\``
                return `${nextIndent}${line}`
              }).join('\n')
              formattedValue = formattedLines
            } else {
              formattedValue = `"${cleanString}"`
            }
          } else {
            formattedValue = formatValue(value, depth + 1)
          }
          
          return `${nextIndent}"${key}": ${formattedValue}`
        }).join(',\n')
        
        return `{\n${items}\n${indent}}`
      }

      if (typeof obj === 'string') {
        return `"${obj.replace(/"/g, '\\"')}"`
      }

      return JSON.stringify(obj)
    }

    return formatValue(data)
  }

</script>

