/**
 * 
 * 
 */

import { ref } from 'vue'
import type { TabType } from '@types'
import { logger } from '@shared/utils/logger'

/**
 * 
 */
export function useEditorTabs(): {
  activeTab: ReturnType<typeof ref<TabType>>
  availableTabs: TabType[]
  switchToTab: (tab: TabType) => void
  isActiveTab: (tab: TabType) => boolean
  showEditor: () => void
  showPartsManager: () => void
  showPreview: () => void
  showImageManager: () => void
} {
  // 
  const activeTab = ref<TabType>('editor')

  /**
   * 
   * @param tab - 
   */
  const switchToTab = (tab: TabType): void => {
    if (activeTab.value !== tab) {
      logger.info(` : ${activeTab.value}  ${tab}`)
      activeTab.value = tab
    }
  }

  /**
   * 
   * @param tab - 
   * @returns  true
   */
  const isActiveTab = (tab: TabType): boolean => {
    return activeTab.value === tab
  }

  /**
   * 
   */
  const showEditor = (): void => {
    switchToTab('editor')
  }

  /**
   * 
   */
  const showPartsManager = (): void => {
    switchToTab('parts')
  }

  /**
   * 
   */
  const showPreview = (): void => {
    switchToTab('preview')
  }

  /**
   * 
   */
  const showImageManager = (): void => {
    switchToTab('images')
  }

  /**
   * 
   */
  const availableTabs: TabType[] = ['editor', 'parts', 'preview', 'images']

  return {
    // 
    activeTab,
    availableTabs,

    // 
    switchToTab,
    isActiveTab,
    showEditor,
    showPartsManager,
    showPreview,
    showImageManager,
  }
}
