import { ref } from 'vue'
import type { Component, ArrayItem, EditingTextState } from '@types'
import {
  processZIf,
  processZForWrapper,
  processZForElements,
  processZForWithRegex,
  processZSlot,
  injectArrayProcessors,
} from './utils/zDirectiveProcessors'
import { restoreVueDirectives, safeDOMParse } from './utils/vueDirectivePreserver'
import {
  processTemplateNotationOptimized,
  getTemplateOptimized,
  clearTemplateCache,
} from './utils/optimizedTemplateProcessor'
import { processZDirectivesOptimized, clearDOMCache } from './utils/optimizedDOMProcessor'
import { errorHandler, safeExecute, ErrorType } from '@shared/utils/errorHandler'
import { escapeJSString, decodeHTMLEntities } from '@shared/utils/htmlHelpers'

// DOMParser
const sharedDOMParser = (() => {
  let instance: DOMParser | null = null
  return () => {
    if (!instance) {
      instance = new DOMParser()
    }
    return instance
  }
})()

export function useTemplateProcessor(): {
  editingText: ReturnType<typeof ref<EditingTextState | null>>
  shouldApplyTiptap: (template: string, paramName: string) => boolean
  processTextReplacementWithTemplate: (
    html: string,
    component: Component,
    template: string
  ) => string
  processArrayItemTextReplacement: (
    itemHtml: string,
    item: ArrayItem,
    component: Component,
    template: string,
    arrayName: string
  ) => string
  processArrayItemParameterReplacement: (itemHtml: string, item: ArrayItem) => string
  processSelectionSyntax: (html: string, component: Component) => string
  processConditionalClassSyntax: (html: string, component: Component) => string
  processZDirectives: (
    html: string,
    component: Component,
    getTemplate: (type: string, moduleName: string) => string,
    _parentIndex?: number,
    currentDepth?: number
  ) => string
  renderComponentWithNested: (
    component: Component,
    parentIndex: number,
    getTemplate: (type: string, moduleName: string) => string,
    renderNestedComponentsWithUICallback: (
      components: Component[],
      currentPath: number[],
      stringifyHierarchyPath: (path: number[]) => string,
      getTemplate?: (type: string, moduleName: string) => string
    ) => string
  ) => string
  renderComponentWithoutSlot: (
    component: Component,
    getTemplate: (type: string, moduleName: string) => string,
    componentIndex?: number
  ) => string
  renderNestedComponentWithDepth: (
    component: Component,
    getTemplate: (type: string, moduleName: string) => string,
    currentDepth: number
  ) => string
  renderNestedComponentsWithUI: (
    components: Component[],
    currentPath: number[],
    stringifyHierarchyPath: (path: number[]) => string,
    getTemplate?: (type: string, moduleName: string) => string
  ) => string
  hasNestedSlot: (
    component: Component,
    getTemplate: (type: string, moduleName: string) => string
  ) => boolean
  renderComponentOptimized: (
    component: Component,
    getTemplate: (type: string, moduleName: string) => string,
    options?: { isPreviewMode?: boolean; maxRecursionDepth?: number }
  ) => string
  clearAllCaches: () => void
} {
  // 
  const editingText = ref<EditingTextState | null>(null)

  // 
  let injectedArrayProcessors = false

  // Tiptap
  const shouldApplyTiptap = (template: string, paramName: string): boolean => {
    // {$param:default}Tiptap
    const paramPattern = `\\{\\$${paramName.replace(/[.*+?^${}()|[\]\\]/g, '\\$&')}:[^}]*\\}`
    const regex = new RegExp(paramPattern, 'g')

    let match
    while ((match = regex.exec(template)) !== null) {
      const matchStart = match.index

      // 
      const beforeMatch = template.substring(0, matchStart)
      const lastOpenBracket = beforeMatch.lastIndexOf('<')
      const lastCloseBracket = beforeMatch.lastIndexOf('>')

      //  '<'  '>' 
      if (lastOpenBracket > lastCloseBracket) {
        // 
        continue
      }

      // 
      const codeBlockRanges: Array<{ start: number; end: number }> = []

      // pre, code 
      const preCodePattern = /<(pre|code)[^>]*>[\s\S]*?<\/(pre|code)>/gi
      // code-block  div
      const codeBlockDivPattern = /<div[^>]*class="[^"]*code-block[^"]*"[^>]*>[\s\S]*?<\/div>/gi

      const patterns = [preCodePattern, codeBlockDivPattern]

      patterns.forEach(pattern => {
        pattern.lastIndex = 0
        let match: RegExpExecArray | null
        while ((match = pattern.exec(template)) !== null) {
          codeBlockRanges.push({
            start: match.index,
            end: match.index + match[0].length,
          })
        }
      })

      // 
      const isInCodeBlock = codeBlockRanges.some(
        range => matchStart >= range.start && matchStart <= range.end
      )

      if (isInCodeBlock) {
        continue
      }

      // 
      return true
    }

    return false
  }

  // 
  const processTextReplacementWithTemplate = (
    html: string,
    component: Component,
    template: string
  ): string => {
    return html.replace(/\{\$([^:]+):(.*?)\}/g, (_match, paramName, defaultValue) => {
      // HTML
      const rawTextValue = String(component[paramName] || defaultValue)
      const textValue = decodeHTMLEntities(rawTextValue)

      // 
      const shouldBeEditable = shouldApplyTiptap(template, paramName)

      if (shouldBeEditable) {
        // content
        if (paramName === 'content') {
          return `<div class="zero-code-rich-editable-text block-editable" data-component-id="${component.id}" data-param="${paramName}" onclick="event.stopPropagation(); if(window.getSelection && window.getSelection().toString().length > 0) return; if(window.startRichTextEdit) window.startRichTextEdit('${component.id}', '${paramName}', this.innerHTML, this);">${textValue}</div>`
        } else {
          return `<span class="zero-code-rich-editable-text inline-editable" data-component-id="${component.id}" data-param="${paramName}" onclick="event.stopPropagation(); if(window.getSelection && window.getSelection().toString().length > 0) return; if(window.startRichTextEdit) window.startRichTextEdit('${component.id}', '${paramName}', this.innerHTML, this);">${textValue}</span>`
        }
      }

      return textValue
    })
  }

  // 
  const processArrayItemTextReplacement = (
    itemHtml: string,
    item: ArrayItem,
    component: Component,
    template: string,
    arrayName: string
  ): string => {
    return itemHtml.replace(
      /\{\$([^:]+):(.*?)\}/g,
      (_innerMatch: string, paramName: string, defaultValue: string) => {
        // 
        const itemValue = item[paramName]
        const hasValue = itemValue !== undefined && itemValue !== null && itemValue !== ''
        const rawTextValue = hasValue ? String(itemValue) : String(defaultValue)
        const textValue = decodeHTMLEntities(rawTextValue)
        
        // 
        if (paramName === 'card_title') {
          console.log(` [] ${paramName}:`, {
            itemValue: itemValue,
            hasValue: hasValue,
            defaultValue: defaultValue,
            finalValue: rawTextValue,
            itemKeys: Object.keys(item),
            itemId: item.id,
            arrayName: arrayName,
            fullItem: item
          })
        }

        // 
        const shouldBeEditable = shouldApplyTiptap(template, paramName)

        if (shouldBeEditable) {
          // ID
          const arrayData = component[arrayName] as ArrayItem[]
          const itemIndex = arrayData.indexOf(item)
          const uniqueItemId = `${component.id}_${arrayName}_${itemIndex}_${paramName}`

          // content
          if (paramName === 'content') {
            return `<div class="zero-code-rich-editable-text array-item-text block-editable" data-component-id="${uniqueItemId}" data-param="${paramName}" data-array-name="${arrayName}" data-item-index="${itemIndex}" onclick="event.stopPropagation(); if(window.getSelection && window.getSelection().toString().length > 0) return; if(window.startRichTextEdit) window.startRichTextEdit('${uniqueItemId}', '${paramName}', this.innerHTML, this);">${textValue}</div>`
          } else {
            return `<span class="zero-code-rich-editable-text array-item-text inline-editable" data-component-id="${uniqueItemId}" data-param="${paramName}" data-array-name="${arrayName}" data-item-index="${itemIndex}" onclick="event.stopPropagation(); if(window.getSelection && window.getSelection().toString().length > 0) return; if(window.startRichTextEdit) window.startRichTextEdit('${uniqueItemId}', '${paramName}', this.innerHTML, this);">${textValue}</span>`
          }
        }

        return textValue
      }
    )
  }

  // 
  const processArrayItemParameterReplacement = (itemHtml: string, item: ArrayItem): string => {
    // 1. : ($param?:option1|option2)
    let result = itemHtml.replace(
      /\(\$([^:?]+)\?:(.*?)\)/g,
      (_match: string, paramName: string, options: string) => {
        const value = item[paramName]
        if (Array.isArray(value)) {
          // 
          return value.join(' ')
        }
        // 
        const optionList = options.split('|').filter((opt: string) => opt.trim())
        return optionList[0] || ''
      }
    )

    // 2. : ($param:option1|option2)
    result = result.replace(
      /\(\$([^:?]+):(.*?)\)/g,
      (_innerMatch: string, paramName: string, options: string) => {
        // 
        if (item[paramName]) {
          let value = item[paramName]
          // 
          if (value === '') {
            value = ''
          }
          // 
          if (Array.isArray(value)) {
            return value.join(' ')
          }
          return String(value)
        }

        // 
        const propKey = paramName
        if (item[propKey]) {
          let value = item[propKey]
          // 
          if (value === '') {
            value = ''
          }
          // 
          if (Array.isArray(value)) {
            return value.join(' ')
          }
          return String(value)
        }

        // 
        const optionList = options.split('|').filter((opt: string) => opt.trim())
        return optionList[0] || ''
      }
    )

    return result
  }

  // 
  const processSelectionSyntax = (html: string, component: Component): string => {
    return html.replace(/\(\$([^:?]+):(.*?)\)/g, (_match, paramName, options) => {
      let value = component[paramName]

      // 
      if (value === '') {
        value = ''
      }

      // 
      if (Array.isArray(value)) {
        return value.join(' ')
      }

      if (value) return String(value)

      // 
      const optionList = options.split('|').filter((opt: string) => opt.trim())
      return optionList[0] || ''
    })
  }

  // 
  const processConditionalClassSyntax = (html: string, component: Component): string => {
    return html.replace(
      /\(\$([^:?]+)\?:(.*?)\)/g,
      (_match: string, paramName: string, _options: string) => {
        const value = component[paramName]
        if (Array.isArray(value)) {
          return value.join(' ')
        }
        return String(value || '')
      }
    )
  }


  // 
  const _renderComponentWithNested = (
    component: Component,
    parentIndex: number,
    getTemplate: (type: string, moduleName: string) => string,
    renderNestedComponentsWithUICallback: (
      components: Component[],
      currentPath: number[],
      stringifyHierarchyPath: (path: number[]) => string,
      getTemplate?: (type: string, moduleName: string) => string
    ) => string
  ): string => {
    const template = getTemplate(component.type, component.module_name)

    if (!template) {
      return `<div>: ${component.type}/${component.module_name}</div>`
    }

    let html = template

    // 1.  ($param:option1|option2|option3)
    html = processSelectionSyntax(html, component)

    // 2.  ($param?:option1|option2|)
    html = processConditionalClassSyntax(html, component)

    // 3.  {$param:}
    html = processTextReplacementWithTemplate(html, component, template)


    // 6. z- (z-for, z-slot, z-if, z-for-wrapper)
    html = processZDirectives(html, component, getTemplate, parentIndex, 1)

    return html
  }

  // 
  const renderComponentWithoutSlot = (
    component: Component,
    getTemplate: (type: string, moduleName: string) => string,
    componentIndex: number = 0
  ): string => {
    // renderComponentWithoutSlot

    const template = getTemplate(component.type, component.module_name)
    if (!template)
      return `<div>: ${component.type}/${component.module_name}</div>`

    let html = template

    // 1.  ($param:option1|option2|option3)
    html = processSelectionSyntax(html, component)

    // 2.  ($param?:option1|option2|)
    html = processConditionalClassSyntax(html, component)

    // 3.  {$param:}
    html = processTextReplacementWithTemplate(html, component, template)

    // 5. 

    // 6. z-
    html = processZDirectives(html, component, getTemplate, componentIndex, 1)

    // renderComponentWithoutSlot
    return html
  }

  // 
  const renderNestedComponentWithDepth = (
    component: Component,
    getTemplate: (type: string, moduleName: string) => string,
    currentDepth: number,
    zForContext?: {
      parentId: string
      arrayName: string
      arrayIndex: number
      slotName: string
    }
  ): string => {
    // renderNestedComponentWithDepth

    const template = getTemplate(component.type, component.module_name)
    if (!template)
      return `<div>: ${component.type}/${component.module_name}</div>`

    let html = template

    // 1.  ($param:option1|option2|option3)
    html = processSelectionSyntax(html, component)

    // 2.  ($param?:option1|option2|)
    html = processConditionalClassSyntax(html, component)

    // 3.  {$param:}
    html = processTextReplacementWithTemplate(html, component, template)

    // 5. 

    // 6. z-
    html = processZDirectives(html, component, getTemplate, 0, currentDepth)

    // renderNestedComponentWithDepth
    return html
  }

  // UI
  const renderNestedComponentsWithUI = (
    components: Component[],
    currentPath: number[] = [],
    stringifyHierarchyPath: (path: number[]) => string,
    getTemplate?: (type: string, moduleName: string) => string
  ): string => {
    if (!components || components.length === 0) {
      return `
        <div class="zero-code-add-nested-part-button-container">
          <button class="zero-code-add-nested-part-button" onclick="window.openNestedPartSelector('${escapeJSString(stringifyHierarchyPath(currentPath))}', 0)">
            <span class="zero-code-plus-icon"></span>
            <span class="zero-code-button-text">${currentPath.length + 1}</span>
          </button>
        </div>
      `
    }

    let html = ''

    components.forEach((child: Component, index: number) => {
      const childPath = [...currentPath, index]
      const pathString = stringifyHierarchyPath(childPath)
      const depth = childPath.length
      const indentLevel = Math.max(0, depth - 1) * 16

      // 
      html += `
        <div class="zero-code-add-nested-part-button-container">
          <button class="zero-code-add-position-part-button" onclick="window.openNestedPartSelector('${escapeJSString(stringifyHierarchyPath(currentPath))}', ${index})">
            <span class="zero-code-plus-icon"></span>
            <span class="zero-code-button-text">${currentPath.length + 1}</span>
          </button>
        </div>
      `

      // 
      html += `
        <div class="zero-code-nested-component zero-code-nested-depth-${depth} zero-code-draggable-nested" 
             draggable="true" 
             data-component-id="${child.id}" 
             data-path="${pathString}" 
             data-parent-path="${stringifyHierarchyPath(currentPath)}"
             data-index="${index}"
>
          <div class="zero-code-component-header zero-code-nested-header">
            <!--  -->
            <div class="zero-code-drag-handle-nested">
              
            </div>
            <div class="zero-code-component-meta">
              <span class="zero-code-component-type">${child.type}</span>
              <span class="zero-code-component-module">${child.module_name}</span>
            </div>
            <div class="zero-code-header-actions">
              <button 
                class="zero-code-collapse-btn"
                onclick="window.toggleNestedCollapse('${pathString}')"
                title=""
                id="collapse-btn-${pathString}"
              >
                
              </button>
              <button class="zero-code-edit-button" onclick="window.openNestedEditor('${pathString}')"></button>
              <button class="zero-code-delete-button" onclick="window.deleteNestedPart('${pathString}')"></button>
            </div>
          </div>
          <div class="zero-code-nested-component-content" id="nested-content-${pathString}">
            ${getTemplate ? renderComponentWithoutSlot(child, getTemplate) : `<div>: ${child.type}/${child.module_name}</div>`}
          </div>
          <script>
            // 
            if (typeof window !== 'undefined' && window.isNestedCollapsed) {
              setTimeout(() => {
                const isCollapsed = window.isNestedCollapsed('${pathString}')
                const contentElement = document.getElementById('nested-content-${pathString}')
                const buttonElement = document.getElementById('collapse-btn-${pathString}')
                
                if (contentElement && buttonElement) {
                  if (isCollapsed) {
                    contentElement.style.display = 'none'
                    buttonElement.textContent = ''
                    buttonElement.setAttribute('title', '')
                  } else {
                    contentElement.style.display = 'block'
                    buttonElement.textContent = ''
                    buttonElement.setAttribute('title', '')
                  }
                }
              }, 0)
            }
          </script>
        </div>
      `

      // 
      if (
        child.nested_parts &&
        Array.isArray(child.nested_parts) &&
        child.nested_parts.length > 0
      ) {
        html += renderNestedComponentsWithUI(
          child.nested_parts,
          childPath,
          stringifyHierarchyPath,
          getTemplate
        )
      }
    })

    // 
    html += `
      <div class="zero-code-add-nested-part-button-container">
        <button class="zero-code-add-position-part-button" onclick="window.openNestedPartSelector('${escapeJSString(stringifyHierarchyPath(currentPath))}', ${components.length})">
          <span class="zero-code-plus-icon"></span>
          <span class="zero-code-button-text">${currentPath.length + 1}</span>
        </button>
      </div>
    `

    return html
  }

  // 

  // z-
  const processZDirectives = (
    html: string,
    component: Component,
    getTemplate: (type: string, moduleName: string) => string,
    _parentIndex: number = 0,
    currentDepth: number = 1
  ): string => {
    return (
      safeExecute(
        () => {
          // z-for

          // 
          if (!injectedArrayProcessors) {
            injectArrayProcessors(
              processArrayItemTextReplacement,
              processArrayItemParameterReplacement
            )
            injectedArrayProcessors = true
          }

          // Vue.jsDOMParser
          const { doc, preservation } = safeDOMParse(html)

          // 
          const renderNestedWithTemplate = (
            comp: Component,
            template: (type: string, moduleName: string) => string,
            depth: number
          ): string => {
            return renderNestedComponentWithDepth(comp, template, depth)
          }

          // z-directive
          processZIf(doc, component)
          processZForWrapper(doc, component)
          
          // if (component.id === 'div_nested_zfor_001') {
          // }
          
          processZForElements(doc, component, currentDepth, renderNestedWithTemplate, getTemplate, undefined, component.id)
          
          // if (component.id === 'div_nested_zfor_001') {
          // }

          // DOMParser
          // DOM
          const originalForElements = preservation.html.match(/z-for="[^"]*"/g)
          const processedForElements = doc.querySelectorAll('[z-for]')

          if (
            originalForElements &&
            originalForElements.length > 0 &&
            processedForElements.length === 0
          ) {
            // DOM
          } else if (processedForElements.length > 0) {
            // Vue.js
            const processedHtml = processZForWithRegex(preservation.html, component)
            if (processedHtml !== preservation.html) {
              // Vue.js
              return restoreVueDirectives(processedHtml, preservation)
            }
          }

          processZSlot(doc, component, currentDepth, renderNestedWithTemplate, getTemplate)

          // 
          const images = doc.querySelectorAll('img')
          images.forEach((img: Element) => {
            img.classList.add('zero-code-editable-image')
            
            // 
            const parent = img.parentElement
            if (parent) {
              parent.classList.add('zero-code-image-container')
            }
            
            // 
            const editButton = doc.createElement('button')
            editButton.className = 'zero-code-image-edit-btn'
            editButton.innerHTML = ''
            editButton.setAttribute('type', 'button')
            editButton.setAttribute('aria-label', '')
            
            // data-field
            if (!img.hasAttribute('data-field')) {
              img.setAttribute('data-field', 'image')
            }
            
            // 
            img.parentNode?.insertBefore(editButton, img.nextSibling)
          })

          // Vue.js
          const processedHtml = doc.body.innerHTML
          const result = restoreVueDirectives(processedHtml, preservation)

          // z-for
          // if (component.id === 'div_nested_zfor_001') {
          //   const hasInnerItems = result.includes('inner_items') || result.includes('1-1')
          // }

          return result
        },
        errorHandler,
        `Z-Directive (componentId: ${component.id})`,
        ErrorType.TEMPLATE
      ) || html
    ) // HTML
  }

  // @slot($nested_parts)@
  const _hasNestedSlot = (
    component: Component,
    getTemplate: (type: string, moduleName: string) => string
  ): boolean => {
    const template = getTemplate(component.type, component.module_name)
    if (!template) return false
    return template.includes('@slot($nested_parts)@')
  }

  // 
  const renderComponentOptimized = (
    component: Component,
    getTemplate: (type: string, moduleName: string) => string,
    options: { isPreviewMode?: boolean; maxRecursionDepth?: number } = {}
  ): string => {
    return (
      safeExecute(
        () => {
          
          if (options && options.isPreviewMode === true) {
            // UI
            const previewOptions = { 
              maxRecursionDepth: options.maxRecursionDepth,
              isPreviewMode: true  // 
            }
            const result = renderComponentForPreview(component, getTemplate, previewOptions)
            return result
          }

          // 
          const template = getTemplateOptimized(component.type, component.module_name, getTemplate)

          if (!template) {
            return `<div>: ${component.type}/${component.module_name}</div>`
          }

          // 41
          let html = processTemplateNotationOptimized(template, component, template)

          // z-directive
          html = processZDirectivesOptimized(html, component, getTemplate, 1)

          return html
        },
        errorHandler,
        ` (componentId: ${component.id})`,
        ErrorType.TEMPLATE
      ) || `<div>: ${component.id}</div>`
    )
  }

  // 
  const renderComponentForPreview = (
    component: Component,
    getTemplate: (type: string, moduleName: string) => string,
    options: { maxRecursionDepth?: number; isPreviewMode?: boolean } = {}
  ): string => {
    // 
    // 
    let latestComponent = component
    if (typeof window !== 'undefined' && (window as any).getLatestComponentData) {
      const latestData = (window as any).getLatestComponentData(component.id)
      if (latestData) {
        latestComponent = latestData
      }
    }
    
    const template = getTemplate(component.type, component.module_name)
    if (!template) return ''

    let html = template

    // 
    const ABSOLUTE_MAX_DEPTH = 50
    
    // 
    let maxDepth = Math.min(
      options.maxRecursionDepth || analyzeZForDepth(template),
      ABSOLUTE_MAX_DEPTH
    )
    
    // 
    maxDepth = Math.max(maxDepth, 1)
    
    // 
    if (options.isPreviewMode) {
      console.log('  - HTML:', html.substring(0, 200))
      
      // UI
      // 1. 
      html = processSelectionSyntax(html, latestComponent)
      console.log(' :', html.substring(0, 200))
      
      // 2. 
      html = processConditionalClassSyntax(html, latestComponent)
      console.log(' :', html.substring(0, 200))
      
      // 3.   
      html = processTextReplacementForPreview(html, latestComponent)
      console.log(' :', html.substring(0, 200))
      
      // 4. z-forUI
      html = processZForForPreviewOnly(html, latestComponent)
      console.log(' z-for:', html.substring(0, 200))
      
      // 5. UI
      html = removeZeroCodeUIClasses(html)
      console.log(' :', html.substring(0, 200))
    } else {
      // 
      // 1. z-directive
      html = processZDirectivesForPreview(html, latestComponent, getTemplate, 0, maxDepth)
      
      // 2. z-directive
      html = processSelectionSyntax(html, latestComponent)

      // 3. 
      html = processConditionalClassSyntax(html, latestComponent)
      
      // 4.   
      html = processTextReplacementForPreview(html, latestComponent)

      // UI
      html = removeZeroCodeUIClasses(html)
    }

    return html
  }

  // z-forz-directive
  const processZDirectivesForPreviewExceptZFor = (
    html: string,
    component: Component,
    getTemplate: (type: string, moduleName: string) => string
  ): string => {
    
    // DOMParserHTML
    const parser = sharedDOMParser()
    const doc = parser.parseFromString(html, 'text/html')

    // z-if
    const ifElements = doc.querySelectorAll('[z-if]')
    ifElements.forEach((element: Element) => {
      const condition = element.getAttribute('z-if')
      if (condition) {
        const shouldShow = component[condition]
        if (!shouldShow) {
          element.remove()
        } else {
          element.removeAttribute('z-if')
        }
      }
    })

    // z-for-wrapper
    const wrapperElements = doc.querySelectorAll('[z-for-wrapper]')
    wrapperElements.forEach((element: Element) => {
      const arrayName = element.getAttribute('z-for-wrapper')
      if (arrayName) {
        const arrayData = component[arrayName]
        if (!Array.isArray(arrayData) || arrayData.length === 0) {
          element.remove()
        } else {
          element.removeAttribute('z-for-wrapper')
        }
      }
    })

    // z-slot
    const slotElements = doc.querySelectorAll('[z-slot]')
    slotElements.forEach((element: Element) => {
      const slotName = element.getAttribute('z-slot')
      if (slotName) {
        if (component[slotName] && Array.isArray(component[slotName])) {
          const nestedComponents = component[slotName] as Component[]
          const nestedHtml = nestedComponents
            .map(child => renderComponentForPreview(child, getTemplate, {}))
            .join('')
          element.outerHTML = nestedHtml
        } else {
          element.remove()
        }
      }
    })

    // 
    const images = doc.querySelectorAll('img')
    images.forEach((img: Element) => {
      img.classList.add('zero-code-editable-image')
      
      // 
      const parent = img.parentElement
      if (parent) {
        parent.classList.add('zero-code-image-container')
      }
      
      // 
      const editButton = doc.createElement('button')
      editButton.className = 'zero-code-image-edit-btn'
      editButton.innerHTML = ''
      editButton.setAttribute('type', 'button')
      editButton.setAttribute('aria-label', '')
      
      // data-field
      if (!img.hasAttribute('data-field')) {
        img.setAttribute('data-field', 'image')
      }
      
      // 
      img.parentNode?.insertBefore(editButton, img.nextSibling)
    })

    return doc.body.innerHTML
  }

  // zero-code UI
  const removeZeroCodeUIClasses = (html: string): string => {
    console.log('  - HTML:', html.substring(0, 300))
    
    // HTML
    let cleanHtml = html
      .replace(/&lt;/g, '<')
      .replace(/&gt;/g, '>')
      .replace(/&amp;/g, '&')
      .replace(/&quot;/g, '"')
      .replace(/&#x27;/g, "'")
    
    console.log(' HTML:', cleanHtml.substring(0, 300))
    
    // DOM
    const parser = sharedDOMParser()
    const doc = parser.parseFromString(cleanHtml, 'text/html')
    
    // zero-code
    const zeroCodeClasses = [
      'zero-code-z-for-item-wrapper',
      'zero-code-field-wrapper', 
      'zero-code-array-item',
      'zero-code-nested-array',
      'field-editor',
      'tiptap-content',
      'zero-code-z-for-controls',
      'zero-code-z-for-controls-inner',
      'zero-code-z-for-btn',
      'zero-code-z-for-add-btn',
      'zero-code-z-for-edit-btn',
      'zero-code-z-for-delete-btn',
      'zero-code-z-for-drag-handle',
      'zero-code-nested-part',
      'zero-code-add-nested-part-button-container',
      'zero-code-add-nested-part-button',
      'zero-code-rich-editable-text',
      'inline-editable',
      'zero-code-editable-image'
    ]
    
    // UI
    const editUISelectors = [
      '.zero-code-z-for-controls',
      '.zero-code-add-nested-part-button-container', 
      '.zero-code-z-for-add-last-button',
      '[onclick*="ZForArrayItem"]',
      '[onclick*="NestedPartSelector"]',
      '[onclick*="NestedEditor"]',
      '[onclick*="addZForArrayItem"]',
      '[onclick*="editZForArrayItem"]',
      '[onclick*="removeZForArrayItem"]',
      '[onclick*="startRichTextEdit"]',
      '.zero-code-rich-editable-text',
      '.inline-editable',
      'span[data-component-id]',
      'span[onclick*="startRichTextEdit"]'
    ]
    
    editUISelectors.forEach(selector => {
      const elements = doc.querySelectorAll(selector)
      elements.forEach(element => {
        // span
        if (element.tagName.toLowerCase() === 'span') {
          element.outerHTML = element.innerHTML
        } else {
          element.remove()
        }
      })
    })
    
    // zero-code
    const allElements = doc.querySelectorAll('*')
    allElements.forEach(element => {
      const classList = element.classList
      zeroCodeClasses.forEach(className => {
        if (classList.contains(className)) {
          classList.remove(className)
        }
      })
      
      // zero-code
      Array.from(classList).forEach(className => {
        if (className.includes('zero-code')) {
          classList.remove(className)
        }
      })
      
      // class
      if (element.className === '') {
        element.removeAttribute('class')
      }
    })
    
    // dataonclick
    allElements.forEach(element => {
      Array.from(element.attributes).forEach(attr => {
        if (attr.name.startsWith('data-component-id') || 
            attr.name.startsWith('data-field-') ||
            attr.name.startsWith('data-param-') ||
            attr.name.startsWith('data-edit-') ||
            attr.name.includes('contenteditable') ||
            attr.name === 'onclick' ||
            attr.name === 'draggable') {
          element.removeAttribute(attr.name)
        }
      })
    })
    
    // span
    const editableSpans = doc.querySelectorAll('span')
    editableSpans.forEach(span => {
      // dataonclickspan
      if (span.hasAttribute('data-component-id') || 
          span.hasAttribute('onclick') ||
          span.classList.contains('zero-code-rich-editable-text') ||
          span.classList.contains('inline-editable')) {
        span.outerHTML = span.innerHTML
      }
    })
    
    // img
    const images = doc.querySelectorAll('img')
    images.forEach(img => {
      const alt = img.getAttribute('alt')
      if (alt && alt.includes('<') && alt.includes('>')) {
        // alt
        const cleanAlt = alt.replace(/<[^>]*>/g, '').replace(/&[^;]+;/g, '').trim()
        img.setAttribute('alt', cleanAlt || '')
      }
    })
    
    return doc.body.innerHTML
  }

  // 
  const processTextReplacementForPreview = (html: string, component: Component): string => {
    return html.replace(/\{\$([^:]+):(.*?)\}/g, (match, paramName, defaultValue) => {
      const textValue = String(component[paramName] || defaultValue)
      return decodeHTMLEntities(textValue)
    })
  }

  // z-forUI
  const processZForForPreviewOnly = (html: string, component: Component): string => {
    const parser = sharedDOMParser()
    const doc = parser.parseFromString(html, 'text/html')

    // z-for
    const zForElements = doc.querySelectorAll('[z-for]')
    zForElements.forEach((element: Element) => {
      const arrayName = element.getAttribute('z-for')
      if (!arrayName) return

      const arrayData = component[arrayName]
      if (!Array.isArray(arrayData) || arrayData.length === 0) {
        element.remove()
        return
      }

      // z-forUI
      let htmlToInsert = ''
      arrayData.forEach((item: any) => {
        let itemHtml = element.outerHTML
        // z-for
        itemHtml = itemHtml.replace(/\s*z-for="[^"]*"/g, '')
        
        // 
        Object.keys(item).forEach(key => {
          const regex = new RegExp(`\\(\\$${key}:([^)]+)\\)`, 'g')
          itemHtml = itemHtml.replace(regex, item[key] || '$1')
        })
        
        htmlToInsert += itemHtml
      })

      element.outerHTML = htmlToInsert
    })

    return doc.body.innerHTML
  }

  // z-for
  const analyzeZForDepth = (template: string): number => {
    const parser = sharedDOMParser()
    const doc = parser.parseFromString(`<root>${template}</root>`, 'text/html')
    
    // 
    const calculateMaxDepth = (element: Element, currentDepth: number = 0): number => {
      let maxDepth = currentDepth
      
      for (const child of Array.from(element.children)) {
        let childDepth = currentDepth
        
        // z-for+1
        if (child.hasAttribute('z-for')) {
          childDepth += 1
        }
        
        // 
        const childMaxDepth = calculateMaxDepth(child, childDepth)
        maxDepth = Math.max(maxDepth, childMaxDepth)
      }
      
      return maxDepth
    }
    
    const rootElement = doc.body.querySelector('root')
    if (!rootElement) return 3 // 
    
    const depth = calculateMaxDepth(rootElement)
    // +2
    return Math.max(depth + 2, 3)
  }

  // z-directive
  const processZDirectivesForPreview = (
    html: string,
    component: Component,
    getTemplate: (type: string, moduleName: string) => string,
    recursionDepth: number = 0,
    maxRecursionDepth?: number
  ): string => {
    
    // DOMParserHTML
    const parser = sharedDOMParser()
    const doc = parser.parseFromString(html, 'text/html')

    // z-if
    const ifElements = doc.querySelectorAll('[z-if]')
    ifElements.forEach((element: Element) => {
      const condition = element.getAttribute('z-if')
      if (condition) {
        const shouldShow = component[condition]
        if (!shouldShow) {
          element.remove()
        } else {
          element.removeAttribute('z-if')
        }
      }
    })

    // z-for-wrapper
    const wrapperElements = doc.querySelectorAll('[z-for-wrapper]')
    wrapperElements.forEach((element: Element) => {
      const arrayName = element.getAttribute('z-for-wrapper')
      if (arrayName) {
        const arrayData = component[arrayName]
        if (!Array.isArray(arrayData) || arrayData.length === 0) {
          element.remove()
        } else {
          element.removeAttribute('z-for-wrapper')
        }
      }
    })

    // 
    const ABSOLUTE_MAX_DEPTH = 50
    
    // 
    if (recursionDepth === 0 && !maxRecursionDepth) {
      const originalTemplate = getTemplate(component.type, component.module_name)
      maxRecursionDepth = Math.min(
        analyzeZForDepth(originalTemplate),
        ABSOLUTE_MAX_DEPTH
      )
    }
    
    // 
    const finalMaxDepth = Math.min(maxRecursionDepth ?? 5, ABSOLUTE_MAX_DEPTH)
    
    // 
    if (recursionDepth >= finalMaxDepth) {
      //  z-for
      return processZDirectivesForPreviewExceptZFor(html, component, getTemplate)
    }
    
    // z-for- 
    let processedAnyZFor = true
    let passCount = 0
    
    while (processedAnyZFor && passCount < 3) { // 3
      processedAnyZFor = false
      passCount++
      
      const forElements = Array.from(doc.querySelectorAll('[z-for]')) as Element[]
      
      
      forElements.forEach((element: Element) => {
        const arrayName = element.getAttribute('z-for')
        
        // 
        if (element.hasAttribute('data-zfor-processed')) {
          return // 
        }
      
      if (arrayName) {
        // z-for
        let sourceComponent = component
        if (typeof window !== 'undefined' && (window as any).getLatestComponentData) {
          // 
          const rootId = component.id.split('_')[0] + '_' + component.id.split('_')[1] + '_' + component.id.split('_')[2] + '_' + component.id.split('_')[3]
          const latestData = (window as any).getLatestComponentData(rootId)
          if (latestData) {
            sourceComponent = latestData
          }
        }
        
        // 
        let arrayData = component[arrayName]
        if (!arrayData && sourceComponent[arrayName]) {
          arrayData = sourceComponent[arrayName]
        }
        
        // z-for
        if (!arrayData && arrayName === 'items') {
          // itemscategories
          if (typeof window !== 'undefined' && (window as any).getLatestComponentData) {
            const rootId = component.id.includes('_categories_') ? component.id.split('_categories_')[0] : 
                          component.id.split('_')[0] + '_' + component.id.split('_')[1] + '_' + component.id.split('_')[2] + '_' + component.id.split('_')[3]
            
            const latestRootData = (window as any).getLatestComponentData(rootId)
            
            if (latestRootData && latestRootData.categories) {
              // ID
              const categoryMatch = component.id.match(/_categories_(\d+)/)
              
              if (categoryMatch) {
                const categoryIndex = parseInt(categoryMatch[1])
                
                if (latestRootData.categories[categoryIndex] && latestRootData.categories[categoryIndex].items) {
                  arrayData = latestRootData.categories[categoryIndex].items
                }
              }
            }
          }
        }
        
        
        
        if (Array.isArray(arrayData) && arrayData.length > 0) {
          // 
          element.setAttribute('data-zfor-processed', 'true')
          // 
          
          // 
          const originalTemplate = getTemplate(component.type, component.module_name)
          const startPattern = new RegExp(`<(\\w+)[^>]*z-for="${arrayName}"[^>]*>`)
          const startMatch = originalTemplate.match(startPattern)
          const startIndex = startMatch ? originalTemplate.indexOf(startMatch[0]) : -1
          
          let templateContent = element.innerHTML // 
          
          if (startIndex !== -1 && startMatch) {
            // 
            const tagMatch = startMatch[0].match(/<(\w+)/)
            const tagName = tagMatch ? tagMatch[1] : 'div'
            let depth = 0
            let endIndex = startIndex
            
            for (let i = startIndex; i < originalTemplate.length; i++) {
              if (originalTemplate.substring(i, i + tagName.length + 1) === `<${tagName}`) {
                depth++
              } else if (originalTemplate.substring(i, i + tagName.length + 3) === `</${tagName}>`) {
                depth--
                if (depth === 0) {
                  endIndex = i + tagName.length + 3
                  break
                }
              }
            }
            
            const fullMatch = originalTemplate.substring(startIndex, endIndex)
            // 
            // z-for
            templateContent = fullMatch.replace(/\s+z-for="[^"]*"/, '')
            
          }
          
          // HTML
          const itemsHtml = arrayData
            .map((item: any, index: number) => {
              let itemHtml = templateContent


              // 
              const mergedComponent = { 
                ...component, 
                ...item,
                // 
                type: component.type,
                module_name: component.module_name,
                id: `${component.id}_${arrayName}_${index}`
              }
              
              // 
              if (typeof window !== 'undefined' && (window as any).getLatestComponentData) {
                const rootId = component.id.includes('_categories_') ? component.id.split('_categories_')[0] : 
                              component.id.includes('_items_') ? component.id.split('_items_')[0] :
                              component.id.split('_')[0] + '_' + component.id.split('_')[1] + '_' + component.id.split('_')[2] + '_' + component.id.split('_')[3]
                const latestRootData = (window as any).getLatestComponentData(rootId)
                if (latestRootData && arrayName === 'categories' && latestRootData.categories && latestRootData.categories[index]) {
                  // categories
                  Object.assign(mergedComponent, latestRootData.categories[index])
                }
              }
              


              
              // z-directive
              itemHtml = processZDirectivesForPreview(itemHtml, mergedComponent, getTemplate, recursionDepth + 1, finalMaxDepth)

              // 
              itemHtml = processSelectionSyntax(itemHtml, item)
              itemHtml = processConditionalClassSyntax(itemHtml, item)

              // mergedComponent
              itemHtml = processTextReplacementForPreview(itemHtml, mergedComponent)

              // itemHtmlHTML
              return itemHtml
            })
            .join('')

          // z-for
          element.outerHTML = itemsHtml
          
          // z-for
          processedAnyZFor = true
          
          // DOMHTML
          const newDoc = sharedDOMParser().parseFromString(doc.body.innerHTML, 'text/html')
          doc.body.innerHTML = newDoc.body.innerHTML
        } else {
          element.remove()
        }
      }
      })
    }

    // z-slot
    const slotElements = doc.querySelectorAll('[z-slot]')
    slotElements.forEach((element: Element) => {
      const slotName = element.getAttribute('z-slot')
      if (slotName) {
        if (component[slotName] && Array.isArray(component[slotName])) {
          const nestedComponents = component[slotName] as Component[]
          const nestedHtml = nestedComponents
            .map(child => renderComponentForPreview(child, getTemplate, {}))
            .join('')
          element.outerHTML = nestedHtml
        } else {
          element.remove()
        }
      }
    })

    // 
    const images = doc.querySelectorAll('img')
    images.forEach((img: Element) => {
      img.classList.add('zero-code-editable-image')
      
      // 
      const parent = img.parentElement
      if (parent) {
        parent.classList.add('zero-code-image-container')
      }
      
      // 
      const editButton = doc.createElement('button')
      editButton.className = 'zero-code-image-edit-btn'
      editButton.innerHTML = ''
      editButton.setAttribute('type', 'button')
      editButton.setAttribute('aria-label', '')
      
      // data-field
      if (!img.hasAttribute('data-field')) {
        img.setAttribute('data-field', 'image')
      }
      
      // 
      img.parentNode?.insertBefore(editButton, img.nextSibling)
    })

    return doc.body.innerHTML
  }

  // 
  const clearAllCaches = (): void => {
    clearTemplateCache()
    clearDOMCache()
    // 
  }

  return {
    editingText,
    shouldApplyTiptap,
    processTextReplacementWithTemplate,
    processArrayItemTextReplacement,
    processArrayItemParameterReplacement,
    processSelectionSyntax,
    processConditionalClassSyntax,
    processZDirectives,
    renderComponentWithNested: _renderComponentWithNested,
    renderComponentWithoutSlot,
    renderNestedComponentWithDepth,
    renderNestedComponentsWithUI,
    hasNestedSlot: _hasNestedSlot,
    // 
    renderComponentOptimized,
    clearAllCaches,
  }
}
