<template>
  <!-- Teleport -->
  <div
    v-if="show"
    class="zero-code-rich-text-toolbar"
    :style="{
      top: position.top + 'px',
      left: position.left + 'px',
    }"
  >
    <div class="zero-code-toolbar-content">
      <!--  -->
      <div class="zero-code-toolbar-section">
        <template v-for="(action, index) in actions" :key="index">
          <div
            v-if="action && action.name === 'separator'"
            class="zero-code-toolbar-separator"
          />
          <button
            v-else-if="action"
            :class="['zero-code-toolbar-btn', { active: action.active, disabled: !isEditing }]"
            :title="action.title || ''"
            :disabled="!isEditing"
            tabindex="-1"
            @click="executeCommand(action.command || '')"
            @mousedown.prevent.stop
          >
            {{ action.icon || '' }}
          </button>
        </template>
      </div>

      <!--  -->
      <div class="zero-code-toolbar-separator" />

      <!--  -->
      <div class="zero-code-toolbar-section">
        <button
          class="zero-code-toolbar-btn zero-code-save-btn"
          title=" (Ctrl+S)"
          tabindex="-1"
          @click="handleSave"
          @mousedown.prevent.stop
        >
          
        </button>
        <button
          class="zero-code-toolbar-btn zero-code-cancel-btn"
          title=" (Esc)"
          tabindex="-1"
          @click="handleCancel"
          @mousedown.prevent.stop
        >
          
        </button>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
  import { watch, onMounted } from 'vue'
  import { logger } from '@shared/utils/logger'

  // Props
  interface Props {
    show: boolean
    position: { top: number; left: number }
    actions: any[]
    editingState: any
    isEditing: boolean
  }

  const props = defineProps<Props>()

  // 
  onMounted(() => {
    logger.debug(' RichTextToolbar mounted')
  })

  // props
  watch(
    () => props.show,
    newShow => {
      logger.debug(' RichTextToolbar show changed:', newShow)
    },
    { immediate: true }
  )

  watch(
    () => props.position,
    newPosition => {
      logger.debug(' RichTextToolbar position changed:', newPosition)
    },
    { deep: true, immediate: true }
  )

  watch(
    () => props.actions,
    newActions => {
      logger.debug(' RichTextToolbar actions changed:', newActions?.length || 0)
    },
    { immediate: true }
  )

  // Emits
  const emit = defineEmits<{
    executeCommand: [command: string]
    save: []
    cancel: []
  }>()

  // 
  const executeCommand = (command: string) => {
    logger.debug(' :', command)
    emit('executeCommand', command)
  }

  // 
  const handleSave = () => {
    emit('save')
  }

  // 
  const handleCancel = () => {
    emit('cancel')
  }
</script>

