  // 
  const updateNestedComponent = (
    components: Component[],
    targetId: string,
    updatedComponent: Component
  ): boolean => {
    logger.debug(
      ` updateNestedComponent:  - targetId: ${targetId}, components: ${components.length}`
    )

    for (let i = 0; i < components.length; i++) {
      const component = components[i]
      logger.debug(` : ${component.id} === ${targetId} ?`)

      if (component.id === targetId) {
        // 
        logger.debug(`  - : ${targetId}`)
        logger.debug(` :`, component)
        logger.debug(` :`, updatedComponent)
        components[i] = updatedComponent
        return true
      }

      // nested_parts
      if (component.nested_parts && Array.isArray(component.nested_parts)) {
        logger.debug(` nested_parts: ${component.id}`)
        const updated = updateNestedComponent(component.nested_parts, targetId, updatedComponent)
        if (updated) return true
      }

      // z-for
      for (const [key, value] of Object.entries(component)) {
        if (Array.isArray(value) && key !== 'nested_parts') {
          logger.debug(` : ${component.id}.${key}`)
          // nested_parts/nested_slots
          for (const arrayItem of value) {
            if (arrayItem && typeof arrayItem === 'object') {
              // 
              for (const [slotKey, slotValue] of Object.entries(arrayItem)) {
                if (Array.isArray(slotValue)) {
                  logger.debug(` : ${component.id}.${key}[].${slotKey}`)
                  const updated = updateNestedComponent(slotValue, targetId, updatedComponent)
                  if (updated) return true
                }
              }
            }
          }
        }
      }
    }
    logger.debug(` : ${targetId}`)
    return false
  }

  const findAndDeleteComponent = (components: Component[], targetId: string): boolean => {
    for (let i = 0; i < components.length; i++) {
      if (components[i].id === targetId) {
        components.splice(i, 1)
        return true
      }
      // nested_parts
      if (components[i].nested_parts && Array.isArray(components[i].nested_parts)) {
        if (findAndDeleteComponent(components[i].nested_parts!, targetId)) {
          return true
        }
      }
      // z-for
      for (const [key, value] of Object.entries(components[i])) {
        if (Array.isArray(value) && key !== 'nested_parts') {
          // nested_parts/nested_slots
          for (const arrayItem of value) {
            if (arrayItem && typeof arrayItem === 'object') {
              // 
              for (const [slotKey, slotValue] of Object.entries(arrayItem)) {
                if (Array.isArray(slotValue)) {
                  if (findAndDeleteComponent(slotValue, targetId)) {
                    return true
                  }
                }
              }
            }
          }
        }
      }
    }
    return false
  }

