<template>
  <div v-if="visible" class="zero-code-modal-overlay" @click="closeModal">
    <div class="zero-code-modal-content zero-code-image-selector-modal" @click.stop>
      <!--  -->
      <div class="zero-code-modal-header">
        <h3 class="zero-code-modal-title"></h3>
        <button class="zero-code-close-button" title="" @click="closeModal">
          
        </button>
      </div>

      <!--  -->
      <div class="zero-code-tab-navigation">
        <button 
          class="zero-code-tab-button"
          :class="{ active: selectedCategory === 'common' }"
          @click="selectedCategory = 'common'"
        >
           ({{ (commonImages || []).length }})
        </button>
        <button 
          class="zero-code-tab-button"
          :class="{ active: selectedCategory === 'individual' }"
          @click="selectedCategory = 'individual'"
        >
           ({{ (individualImages || []).length }})
        </button>
      </div>

      <!--  -->
      <div class="zero-code-search-section">
        <input
          v-model="searchQuery"
          type="text"
          placeholder="..."
          class="zero-code-search-input"
        >
      </div>

      <!--  -->
      <div class="zero-code-modal-body">
        <div v-if="filteredImages.length === 0" class="zero-code-empty-state">
          <p></p>
        </div>
        
        <div v-else class="zero-code-image-grid">
          <div
            v-for="image in filteredImages"
            :key="image.id"
            class="zero-code-image-item"
            :class="{ selected: selectedImage?.id === image.id }"
            :title="selectedImage?.id === image.id ? '' : ''"
            @click="selectImage(image)"
          >
            <div class="zero-code-image-preview">
              <img
                :src="getImageSrc(image)"
                :alt="getImageAlt(image)"
                loading="lazy"
                @error="handleImageError($event, image)"
              >
              <div v-if="isImageUpdated(image)" class="zero-code-img-updated-badge">
                
              </div>
              <div 
                v-if="selectedImage?.id === image.id" 
                class="zero-code-selection-indicator"
              >
                <span class="zero-code-check-icon"></span>
              </div>
            </div>
            <div class="zero-code-image-info">
              <div class="zero-code-image-name">{{ image.name }}</div>
              <div class="zero-code-image-alt">{{ image.alt }}</div>
            </div>
          </div>
        </div>
      </div>

      <!--  -->
      <div v-if="selectedImage" class="zero-code-image-preview-section">
        <div class="zero-code-preview-header">
          <h4 class="zero-code-preview-title"></h4>
          <button 
            class="zero-code-clear-selection-btn" 
            title=""
            @click="clearSelection"
          >
            <span class="zero-code-clear-icon"></span>
            
          </button>
        </div>
        <div class="zero-code-selected-preview">
          <img :src="getImageSrc(selectedImage)" :alt="getImageAlt(selectedImage)">
          <div class="zero-code-selected-info">
            <div class="zero-code-info-item">
              <label class="zero-code-info-label"></label>
              <strong>{{ selectedImage.name }}</strong>
            </div>
            <div class="zero-code-info-item">
              <label class="zero-code-info-label"></label>
              <p>{{ selectedImage.alt || '' }}</p>
            </div>
            <div class="zero-code-info-item">
              <label class="zero-code-info-label">URL</label>
              <code>{{ selectedImage.url }}</code>
            </div>
            <div v-if="isImageUpdated(selectedImage)" class="zero-code-info-item">
              <label class="zero-code-info-label"></label>
              <span class="zero-code-status-badge"></span>
            </div>
          </div>
        </div>
      </div>

      <!--  -->
      <div class="zero-code-modal-footer">
        <div class="zero-code-modal-footer-left">
          <button 
            class="zero-code-btn zero-code-btn-primary zero-code-upload-btn"
            @click="openFileUpload"
            title=""
          >
             
          </button>
        </div>
        <div class="zero-code-modal-footer-right">
          <button class="zero-code-btn zero-code-btn-secondary" @click="closeModal">
            
          </button>
          <button 
            class="zero-code-btn zero-code-btn-primary"
            :disabled="!selectedImage"
            @click="applySelection"
          >
            {{ selectedImage && isImageUpdated(selectedImage) ? '' : '' }}
          </button>
        </div>
        
        <!--  -->
        <input
          ref="fileInputRef"
          type="file"
          accept="image/*"
          style="display: none"
          @change="handleFileUpload"
        >
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, watch } from 'vue'
import type { SampleImage } from '../../data/types'
import { useImageDisplay } from '../../composables/useImageDisplay'

// Props
interface Props {
  visible: boolean
  commonImages?: SampleImage[]
  individualImages?: SampleImage[]
  currentValue?: string
}

const props = withDefaults(defineProps<Props>(), {
  commonImages: () => [],
  individualImages: () => []
})

// Emits
interface Emits {
  (e: 'close'): void
  (e: 'select', imageUrl: string, imageAlt: string): void
  (e: 'upload', imageUrl: string, imageAlt: string): void
}

const emit = defineEmits<Emits>()

// 
const { getImageSrc, getImageAlt, isImageUpdated, updateImageData } = useImageDisplay()

// State
const selectedCategory = ref<'common' | 'individual'>('common')
const searchQuery = ref('')
const selectedImage = ref<SampleImage | null>(null)
const fileInputRef = ref<HTMLInputElement | null>(null)

// Computed
const currentImages = computed(() => {
  return selectedCategory.value === 'common' 
    ? (props.commonImages || [])
    : (props.individualImages || [])
})

const filteredImages = computed(() => {
  const query = searchQuery.value.toLowerCase()
  if (!query) return currentImages.value
  
  return currentImages.value.filter(image => 
    image.name.toLowerCase().includes(query) ||
    (image.alt && image.alt.toLowerCase().includes(query)) ||
    image.url.toLowerCase().includes(query)
  )
})

// Methods
const closeModal = () => {
  emit('close')
}

const selectImage = (image: SampleImage) => {
  // 
  if (selectedImage.value?.id === image.id) {
    selectedImage.value = null
  } else {
    selectedImage.value = image
  }
}

const clearSelection = () => {
  selectedImage.value = null
}

const applySelection = () => {
  if (selectedImage.value) {
    // URLBase64
    // URL
    const hasBase64 = !!(selectedImage.value.fileData?.base64 && selectedImage.value.fileData.base64.trim() !== '')
    
    if (hasBase64) {
      // Base64Base64 URL
      const resolvedUrl = getImageSrc(selectedImage.value)
      const resolvedAlt = getImageAlt(selectedImage.value)
      emit('select', resolvedUrl, resolvedAlt)
    } else {
      // URL
      emit('select', selectedImage.value.url, selectedImage.value.alt || '')
    }
    closeModal()
  }
}

const handleImageError = (event: Event, image: SampleImage) => {
  const target = event.target as HTMLImageElement
  target.src = '/img/placeholder.jpg' // 
  console.warn(`: ${image.url}`)
}

// Watchers
watch(() => props.visible, (newVisible) => {
  if (newVisible) {
    // 
    if (props.currentValue) {
      const allImages = [...(props.commonImages || []), ...(props.individualImages || [])]
      const currentImage = allImages.find(img => img.url === props.currentValue)
      if (currentImage) {
        selectedImage.value = currentImage
        selectedCategory.value = currentImage.category as 'common' | 'individual'
      }
    }
    // 
    searchQuery.value = ''
  } else {
    // 
    selectedImage.value = null
  }
})

watch(selectedCategory, () => {
  // 
  selectedImage.value = null
})

// 

// 
const openFileUpload = () => {
  if (fileInputRef.value) {
    fileInputRef.value.click()
  }
}

// 
const handleFileUpload = async (event: Event) => {
  const target = event.target as HTMLInputElement
  if (!target.files || !target.files[0]) return

  const file = target.files[0]
  
  if (!confirm(` "${file.name}" `)) {
    // 
    target.value = ''
    return
  }

  try {
    // Base64
    const base64 = await fileToBase64(file)
    const base64Data = base64.split(',')[1] // data:image/...;base64, 
    
    // Base64 URL
    const imageUrl = base64
    const imageAlt = file.name.replace(/\.[^/.]+$/, '') // alt
    
    // 
    emit('upload', imageUrl, imageAlt)
    closeModal()
  } catch (error) {
    alert('')
    console.error(':', error)
  } finally {
    // 
    target.value = ''
  }
}

// Base64
const fileToBase64 = (file: File): Promise<string> => {
  return new Promise((resolve, reject) => {
    const reader = new FileReader()
    reader.readAsDataURL(file)
    reader.onload = () => resolve(reader.result as string)
    reader.onerror = error => reject(error)
  })
}
</script>