/**
 * 
 * 
 */

import { logger } from './logger'

export enum ErrorType {
  VALIDATION = 'VALIDATION',
  NETWORK = 'NETWORK',
  COMPONENT = 'COMPONENT',
  TEMPLATE = 'TEMPLATE',
  FILE = 'FILE',
  PERMISSION = 'PERMISSION',
  IMAGE_LOAD_FAILED = 'IMAGE_LOAD_FAILED',
  UNKNOWN = 'UNKNOWN',
  // 
  NETWORK_ERROR = 'NETWORK_ERROR',
  VALIDATION_ERROR = 'VALIDATION_ERROR',
  PERMISSION_ERROR = 'PERMISSION_ERROR',
  COMPONENT_ERROR = 'COMPONENT_ERROR',
  TEMPLATE_PARSE_ERROR = 'TEMPLATE_PARSE_ERROR',
  UNKNOWN_ERROR = 'UNKNOWN_ERROR',
}

export interface EditorErrorData {
  type: ErrorType
  message: string
  details?: Record<string, unknown>
  componentId?: string
  action?: string
  timestamp: number
}

export class EditorErrorHandler {
  private errorHistory: EditorErrorData[] = []
  private maxHistorySize = 100

  /**
   * 
   */
  handleError(
    type: ErrorType,
    message: string,
    details?: Record<string, unknown>,
    componentId?: string,
    action?: string
  ): EditorErrorData {
    const error: EditorErrorData = {
      type,
      message,
      details,
      componentId,
      action,
      timestamp: Date.now(),
    }

    // 
    this.errorHistory.unshift(error)
    if (this.errorHistory.length > this.maxHistorySize) {
      this.errorHistory = this.errorHistory.slice(0, this.maxHistorySize)
    }

    // 
    const isUserFacing = type === ErrorType.VALIDATION || type === ErrorType.PERMISSION

    if (isUserFacing) {
      logger.warn(`[${type}] ${message}`, details)
    } else {
      logger.error(`[${type}] ${message}`, details)
    }

    return error
  }

  /**
   * 
   */
  componentError(message: string, componentId?: string, details?: Record<string, unknown>): EditorErrorData {
    return this.handleError(
      ErrorType.COMPONENT,
      message,
      details,
      componentId,
      'component_operation'
    )
  }

  /**
   * 
   */
  templateError(message: string, details?: Record<string, unknown>): EditorErrorData {
    return this.handleError(ErrorType.TEMPLATE, message, details, undefined, 'template_processing')
  }

  /**
   * 
   */
  validationError(message: string, details?: Record<string, unknown>): EditorErrorData {
    return this.handleError(ErrorType.VALIDATION, message, details, undefined, 'validation')
  }

  /**
   * 
   */
  fileError(message: string, details?: Record<string, unknown>): EditorErrorData {
    return this.handleError(ErrorType.FILE, message, details, undefined, 'file_operation')
  }

  /**
   * 
   */
  networkError(message: string, details?: Record<string, unknown>): EditorErrorData {
    return this.handleError(ErrorType.NETWORK, message, details, undefined, 'network_request')
  }

  /**
   * 
   */
  permissionError(message: string, details?: Record<string, unknown>): EditorErrorData {
    return this.handleError(ErrorType.PERMISSION, message, details, undefined, 'permission_check')
  }

  /**
   * 
   */
  unknownError(message: string, details?: Record<string, unknown>): EditorErrorData {
    return this.handleError(ErrorType.UNKNOWN, message, details, undefined, 'unknown')
  }

  /**
   * 
   */
  getErrorHistory(): EditorErrorData[] {
    return [...this.errorHistory]
  }

  /**
   * 
   */
  getErrorsByType(type: ErrorType): EditorErrorData[] {
    return this.errorHistory.filter(error => error.type === type)
  }

  /**
   * 
   */
  clearHistory(): void {
    this.errorHistory = []
    logger.debug('')
  }

  /**
   * 
   */
  getErrorStats(): Record<ErrorType, number> {
    const stats = Object.values(ErrorType).reduce(
      (acc, type) => {
        acc[type] = 0
        return acc
      },
      {} as Record<ErrorType, number>
    )

    this.errorHistory.forEach(error => {
      stats[error.type]++
    })

    return stats
  }
}

/**
 * 
 */
export function safeExecute<T>(
  fn: () => T,
  errorHandler: EditorErrorHandler,
  errorMessage: string,
  errorType: ErrorType = ErrorType.UNKNOWN
): T | null {
  try {
    return fn()
  } catch (error) {
    const errorDetails = error instanceof Error ? { stack: error.stack, name: error.name } : { error: String(error) }
    errorHandler.handleError(errorType, errorMessage, errorDetails)
    return null
  }
}

/**
 * 
 */
export async function safeExecuteAsync<T>(
  fn: () => Promise<T>,
  errorHandler: EditorErrorHandler,
  errorMessage: string,
  errorType: ErrorType = ErrorType.UNKNOWN
): Promise<T | null> {
  try {
    return await fn()
  } catch (error) {
    const errorDetails = error instanceof Error ? { stack: error.stack, name: error.name } : { error: String(error) }
    errorHandler.handleError(errorType, errorMessage, errorDetails)
    return null
  }
}

/**
 * 
 */
export async function retryOperation<T>(
  fn: () => Promise<T>,
  maxRetries: number = 3,
  delay: number = 1000,
  errorHandler?: EditorErrorHandler
): Promise<T | null> {
  let lastError: any

  for (let attempt = 1; attempt <= maxRetries; attempt++) {
    try {
      return await fn()
    } catch (error) {
      lastError = error

      if (attempt < maxRetries) {
        logger.warn(`... (${attempt}/${maxRetries})`, error)
        await new Promise(resolve => setTimeout(resolve, delay * attempt))
      }
    }
  }

  if (errorHandler) {
    errorHandler.unknownError(`${maxRetries}`, lastError)
  }

  return null
}

// 
export const errorHandler = new EditorErrorHandler()

export default errorHandler
