/**
 *  Composable
 * 
 */

import { ref, readonly, computed } from 'vue'
import type { 
  SampleDataSet, 
  DataConfig, 
  DataEnvironment
} from '../data/types'
import { sampleDataSet } from '../data/samples'
import { testDataSet } from '../data/tests'
import { logger } from '../shared/utils/logger'

// 
const dataConfig = ref<DataConfig>({
  environment: 'development',
  enableMockData: true,
  defaultDataSet: 'samples',
})

// 
const currentDataSet = ref<SampleDataSet>(sampleDataSet)

// 
const isLoading = ref(false)
const loadError = ref<string | null>(null)

/**
 *  Composable
 */
export function useDataLoader() {
  
  // 
  const setEnvironment = (env: DataEnvironment) => {
    dataConfig.value.environment = env
    logger.info(`  ${env} `)
  }

  // 
  const loadData = async (dataSetName: string = 'samples'): Promise<boolean> => {
    isLoading.value = true
    loadError.value = null
    
    try {
      // 
      if (dataSetName === 'samples') {
        currentDataSet.value = sampleDataSet
      } else if (dataSetName === 'tests' || dataSetName === 'zfor-zslot') {
        currentDataSet.value = testDataSet
      } else {
        throw new Error(`: ${dataSetName}`)
      }
      
      return true
      
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : ''
      loadError.value = errorMessage
      logger.error(` : ${errorMessage}`)
      return false
      
    } finally {
      isLoading.value = false
    }
  }


  // 
  const parts = computed(() => currentDataSet.value.parts)
  const pages = computed(() => currentDataSet.value.pages)
  const images = computed(() => currentDataSet.value.images)
  
  // 
  const combinedParts = computed(() => [
    ...currentDataSet.value.parts.common,
    ...currentDataSet.value.parts.individual,
  ])

  // 
  const defaultPageData = computed(() => 
    currentDataSet.value.pages[0]?.components || []
  )

  // 
  const state = readonly({
    isLoading,
    loadError,
    config: readonly(dataConfig),
  })

  // 
  const initialize = async () => {
    // TypeScript 
    await loadData('samples')
  }

  return {
    // 
    parts,
    pages,
    images,
    combinedParts,
    defaultPageData,
    
    
    // 
    state,
    
    // 
    setEnvironment,
    loadData,
    initialize,
  }
}