import { logger } from './logger'

export interface EditorEventData {
  // 
  addArrayItem: {
    componentId: string
    arrayKey: string
  }
  // z-for
  addZForArrayItem: {
    componentId: string
    arrayName: string
    insertIndex: number | string  // stableItemId
  }
  // z-for
  removeZForArrayItem: {
    componentId: string
    arrayName: string
    index: number | string  // stableItemId
  }
  // z-for
  reorderZForArrayItem: {
    componentId: string
    arrayName: string
    fromIndex: number
    toIndex: number
  }
  // z-for
  editZForArrayItem: {
    componentId: string
    arrayName: string
    index: number | string  // stableItemId
  }
  // 
  deleteNestedPart: {
    pathString: string
  }
  // 
  openNestedEditor: {
    pathString: string
  }
  // 
  openNestedPartSelector: {
    pathString: string
    position: number
  }
  // 
  startRichTextEdit: {
    componentId: string
    paramName: string
    currentValue: string
  }
  // 
  selectImage: {
    componentId: string
    paramName: string
  }
  // 
  uploadImage: {
    file: File
    category: 'common' | 'individual'
    componentId?: string
  }
  // 
  editorClosed: {
    componentId?: string
    componentIndex?: number | string | null
  }
}

type EventCallback<T = any> = (data: T) => void | Promise<void>

export class EditorEventBus {
  private events: Map<string, Set<EventCallback>> = new Map()
  private isEnabled = true

  /**
   * 
   */
  on<K extends keyof EditorEventData>(
    event: K,
    callback: EventCallback<EditorEventData[K]>
  ): () => void {
    if (!this.events.has(event)) {
      this.events.set(event, new Set())
    }

    const listeners = this.events.get(event)!
    listeners.add(callback as EventCallback)


    // 
    return () => {
      listeners.delete(callback as EventCallback)
    }
  }

  /**
   * 
   */
  emit<K extends keyof EditorEventData>(event: K, data: EditorEventData[K]): void {
    if (!this.isEnabled) {
      logger.warn(`EventBus:  ${event} `)
      return
    }

    const listeners = this.events.get(event)
    if (!listeners || listeners.size === 0) {
      logger.warn(`EventBus: ${event} `)
      return
    }


    // 
    const promises: Promise<void>[] = []

    listeners.forEach(callback => {
      try {
        const result = callback(data)
        if (result instanceof Promise) {
          promises.push(result)
        }
      } catch (error) {
        logger.error(`EventBus: ${event} `, error)
      }
    })

    // Promise
    if (promises.length > 0) {
      Promise.allSettled(promises).then(results => {
        results.forEach((result, index) => {
          if (result.status === 'rejected') {
            logger.error(`EventBus: ${event} [${index}]`, result.reason)
          }
        })
      })
    }
  }

  /**
   * 
   */
  off(event: keyof EditorEventData): void {
    this.events.delete(event)
  }

  /**
   * 
   */
  clear(): void {
    this.events.clear()
  }

  /**
   * 
   */
  disable(): void {
    this.isEnabled = false
  }

  /**
   * 
   */
  enable(): void {
    this.isEnabled = true
  }

  /**
   * 
   */
  getListenerCount(event?: keyof EditorEventData): number {
    if (event) {
      return this.events.get(event)?.size || 0
    }

    let total = 0
    this.events.forEach(listeners => {
      total += listeners.size
    })
    return total
  }
}

// 
export const editorEventBus = new EditorEventBus()

// 
export type EditorEventBusType = EditorEventBus

/**
 * HTML
 * EventBus
 */
export const globalUIFunctions = {
  /**
   * 
   */
  addArrayItem: (componentId: string, arrayKey: string) => {
    editorEventBus.emit('addArrayItem', { componentId, arrayKey })
  },

  /**
   * z-for
   */
  addZForArrayItem: (componentId: string, arrayName: string, insertIndex: number | string) => {
    editorEventBus.emit('addZForArrayItem', { componentId, arrayName, insertIndex })
  },

  /**
   * z-for
   */
  removeZForArrayItem: (componentId: string, arrayName: string, index: number) => {
    editorEventBus.emit('removeZForArrayItem', { componentId, arrayName, index })
  },

  /**
   * z-for
   */
  reorderZForArrayItem: (
    componentId: string,
    arrayName: string,
    fromIndex: number,
    toIndex: number
  ) => {
    editorEventBus.emit('reorderZForArrayItem', { componentId, arrayName, fromIndex, toIndex })
  },

  /**
   * z-for
   */
  editZForArrayItem: (componentId: string, arrayName: string, index: number) => {
    editorEventBus.emit('editZForArrayItem', { componentId, arrayName, index })
  },

  /**
   * 
   */
  deleteNestedPart: (pathString: string) => {
    editorEventBus.emit('deleteNestedPart', { pathString })
  },

  /**
   * 
   */
  openNestedEditor: (pathString: string) => {
    editorEventBus.emit('openNestedEditor', { pathString })
  },

  /**
   * 
   */
  openNestedPartSelector: (pathString: string, position: number) => {
    editorEventBus.emit('openNestedPartSelector', { pathString, position })
  },

  /**
   * 
   */
  startRichTextEdit: (componentId: string, paramName: string, currentValue: string) => {
    return editorEventBus.emit('startRichTextEdit', { componentId, paramName, currentValue })
  },

  /**
   * 
   */
  selectImage: (componentId: string, paramName: string) => {
    editorEventBus.emit('selectImage', { componentId, paramName })
  },

  /**
   * 
   */
  uploadImage: (file: File, category: 'common' | 'individual', componentId?: string) => {
    editorEventBus.emit('uploadImage', { file, category, componentId })
  },
}

export default editorEventBus
