<template>
  <div class="zero-code-error-boundary">
    <!--  -->
    <div v-if="hasErrors" class="zero-code-error-notifications">
      <div
        v-for="error in errors"
        :key="error.id"
        class="zero-code-error-notification"
        :class="getErrorClass(error.type)"
      >
        <div class="zero-code-error-content">
          <div class="zero-code-error-message">
            <span class="zero-code-error-icon">{{ getErrorIcon(error.type) }}</span>
            <span class="zero-code-error-text">{{ error.message }}</span>
          </div>
          <button class="zero-code-error-close" :title="''" @click="clearError(error.id)">
            
          </button>
        </div>
        <div v-if="error.details && showDetails" class="zero-code-error-details">
          <pre class="zero-code-error-details-pre">{{ error.details }}</pre>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="zero-code-error-boundary-content">
      <slot />
    </div>
  </div>
</template>

<script setup lang="ts">
  import { useErrorHandler, ErrorType } from '../../composables/useErrorHandler'

  // Props
  interface Props {
    showDetails?: boolean
  }

  const props = withDefaults(defineProps<Props>(), {
    showDetails: false,
  })

  // 
  const { errors, hasErrors, clearError } = useErrorHandler()

  // CSS
  const getErrorClass = (type: ErrorType): string => {
    switch (type) {
      case ErrorType.NETWORK_ERROR:
        return 'error-network'
      case ErrorType.VALIDATION_ERROR:
        return 'error-validation'
      case ErrorType.PERMISSION_ERROR:
        return 'error-permission'
      case ErrorType.COMPONENT_ERROR:
        return 'error-component'
      case ErrorType.IMAGE_LOAD_FAILED:
        return 'error-image'
      case ErrorType.TEMPLATE_PARSE_ERROR:
        return 'error-template'
      default:
        return 'error-unknown'
    }
  }

  // 
  const getErrorIcon = (type: ErrorType): string => {
    switch (type) {
      case ErrorType.NETWORK_ERROR:
        return ''
      case ErrorType.VALIDATION_ERROR:
        return ''
      case ErrorType.PERMISSION_ERROR:
        return ''
      case ErrorType.COMPONENT_ERROR:
        return ''
      case ErrorType.IMAGE_LOAD_FAILED:
        return ''
      case ErrorType.TEMPLATE_PARSE_ERROR:
        return ''
      default:
        return ''
    }
  }
</script>
