<template>
  <div class="zero-code-tab-navigation">
    <button
      v-for="tab in tabs"
      :key="tab"
      :class="['zero-code-tab-button', { active: activeTab === tab }]"
      @click="$emit('update:active-tab', tab)"
    >
      {{ getTabLabel(tab) }}
    </button>
  </div>
</template>

<script setup lang="ts">
  import type { TabType } from '@types'

  // Props
  defineProps<{
    activeTab: TabType
  }>()

  // Emits
  defineEmits<{
    'update:active-tab': [tab: TabType]
  }>()

  // 
  const tabs = ['editor', 'preview', 'parts', 'images'] as const

  // 
  const getTabLabel = (tab: TabType): string => {
    const labels: Record<TabType, string> = {
      editor: '',
      preview: '',
      parts: '',
      images: '',
    }
    return labels[tab]
  }
</script>
