/**
 * HTML
 * @param str HTML
 * @returns 
 */
export function decodeHTMLEntities(str: string): string {
  if (!str) return ''
  return str
    .replace(/&lt;/g, '<')
    .replace(/&gt;/g, '>')
    .replace(/&amp;/g, '&')
    .replace(/&quot;/g, '"')
    .replace(/&#39;/g, "'")
    .replace(/&nbsp;/g, ' ')
}

/**
 * JavaScript
 * @param str 
 * @returns 
 */
export function escapeJSString(str: string): string {
  if (!str) return ''
  return str
    .replace(/\\/g, '\\\\') // 
    .replace(/'/g, "\\'") // 
    .replace(/"/g, '\\"') // 
    .replace(/\n/g, '\\n') // 
    .replace(/\r/g, '\\r') // 
    .replace(/\t/g, '\\t') // 
    .replace(/</g, '\\x3C') // <XSS
    .replace(/>/g, '\\x3E') // >XSS
    .replace(/&/g, '\\x26') // &
    .replace(/\0/g, '\\0') // NULL
}

/**
 * HTML
 * @param str 
 * @returns 
 */
export function escapeHTMLAttribute(str: string): string {
  if (!str) return ''
  return str
    .replace(/&/g, '&amp;')
    .replace(/"/g, '&quot;')
    .replace(/'/g, '&#39;')
    .replace(/</g, '&lt;')
    .replace(/>/g, '&gt;')
    .replace(/\n/g, '&#10;')
    .replace(/\r/g, '&#13;')
    .replace(/\t/g, '&#9;')
}

/**
 * HTML
 * @param str HTML
 * @returns 
 */
export function stripHtmlTags(str: string): string {
  if (!str) return ''
  return str.replace(/<[^>]*>/g, '')
}

/**
 * HTML
 * @param html HTML
 * @returns HTML
 */
export function cleanupHtmlContent(html: string): string {
  if (!html) return html

  // <br><br>
  let cleaned = html.replace(/(<br\s*\/?>)+$/gi, '')

  // 
  cleaned = cleaned.replace(/\s+$/, '')

  return cleaned
}