import { logger } from '@shared/utils/logger'
import type { Component } from '@types'

// 
export interface MemoizedFunction<TArgs extends unknown[], TReturn> {
  (...args: TArgs): TReturn
  clearCache: () => void
}

export function memoize<TArgs extends unknown[], TReturn>(
  fn: (...args: TArgs) => TReturn,
  keyFn?: (...args: TArgs) => string,
  maxCacheSize = 100
): MemoizedFunction<TArgs, TReturn> {
  const cache = new Map<string, TReturn>()

  const memoizedFn = (...args: TArgs): TReturn => {
    const key = keyFn ? keyFn(...args) : JSON.stringify(args)

    if (cache.has(key)) {
      logger.debug(':', key)
      return cache.get(key)!
    }

    const result = fn(...args)

    // 
    if (cache.size >= maxCacheSize) {
      const firstKey = cache.keys().next().value
      if (firstKey) {
        cache.delete(firstKey)
      }
    }

    cache.set(key, result)
    logger.debug(':', key)

    return result
  }

  // 
  memoizedFn.clearCache = () => {
    cache.clear()
    logger.debug('')
  }

  return memoizedFn
}

// 
export const memoizedComponentOperations = {
  // 
  findComponent: memoize(
    (components: Component[], targetId: string): Component | null => {
      const findComponentById = (components: Component[], targetId: string): Component | null => {
        for (const comp of components) {
          if (comp.id === targetId) {
            return comp
          }
          
          // 
          if (comp.nested_parts && comp.nested_parts.length > 0) {
            const nested = findComponentById(comp.nested_parts, targetId)
            if (nested) return nested
          }
          
          // z-for
          for (const key of Object.keys(comp)) {
            if (Array.isArray(comp[key])) {
              for (let index = 0; index < comp[key].length; index++) {
                const item = comp[key][index]
                if (item && typeof item === 'object' && item.nested_parts) {
                  const deepNested = findComponentById(item.nested_parts, targetId)
                  if (deepNested) {
                    return deepNested
                  }
                }
              }
            }
          }
        }
        return null
      }
      
      return findComponentById(components, targetId)
    },
    (components, targetId) => `${targetId}_${components.length}_${components.map(c => c.id).join('_')}`,
    50
  ),

  // 
  getComponentPath: memoize(
    (components: Component[], targetId: string, currentPath: number[] = []): number[] | null => {
      for (let i = 0; i < components.length; i++) {
        const component = components[i]
        const newPath = [...currentPath, i]

        if (component.id === targetId) {
          return newPath
        }

        if (component.nested_parts) {
          const found = memoizedComponentOperations.getComponentPath(
            component.nested_parts,
            targetId,
            newPath
          )
          if (found) return found
        }
      }
      return null
    },
    (components, targetId, currentPath) =>
      `path_${targetId}_${components.length}_${currentPath?.join('_') || ''}`,
    30
  ),

  // 
  getComponentDepth: memoize(
    (components: Component[], targetId: string, depth: number = 0): number => {
      for (const component of components) {
        if (component.id === targetId) {
          return depth
        }
        if (component.nested_parts) {
          const found = memoizedComponentOperations.getComponentDepth(
            component.nested_parts,
            targetId,
            depth + 1
          )
          if (found >= 0) return found
        }
      }
      return -1
    },
    (components, targetId, depth: number = 0) => `depth_${targetId}_${depth}_${components.length}`,
    30
  ),

  // 
  clearAllCaches: () => {
    memoizedComponentOperations.findComponent.clearCache()
    memoizedComponentOperations.getComponentPath.clearCache()
    memoizedComponentOperations.getComponentDepth.clearCache()
    logger.debug('')
  }
}

// 
export function debounce<TArgs extends unknown[], TReturn>(
  func: (...args: TArgs) => TReturn,
  wait: number
): (...args: TArgs) => void {
  let timeout: ReturnType<typeof setTimeout> | null = null

  return (...args: TArgs) => {
    if (timeout) {
      clearTimeout(timeout)
    }

    timeout = setTimeout(() => {
      func(...args)
      timeout = null
    }, wait)
  }
}

// 
export function throttle<T extends (...args: unknown[]) => unknown>(
  func: T,
  limit: number
): T {
  let inThrottle = false

  return ((...args: Parameters<T>) => {
    if (!inThrottle) {
      func(...args)
      inThrottle = true
      setTimeout(() => {
        inThrottle = false
      }, limit)
    }
  }) as T
}

// 
export const optimizedArrayOperations = {
  // 
  batchUpdate: <T>(
    array: T[],
    updates: Array<{ index: number; value: T }>
  ): T[] => {
    const result = [...array]
    updates.forEach(({ index, value }) => {
      if (index >= 0 && index < result.length) {
        result[index] = value
      }
    })
    logger.debug(`: ${updates.length}`)
    return result
  },

  // 
  findIndex: memoize(
    <T>(array: T[], predicate: (item: T) => boolean): number => {
      return array.findIndex(predicate)
    },
    (array, predicate) => `${array.length}_${predicate.toString()}`,
    20
  )
}

// 
export class CacheManager {
  private caches = new Map<string, Map<string, unknown>>()

  getCache(cacheName: string): Map<string, unknown> {
    if (!this.caches.has(cacheName)) {
      this.caches.set(cacheName, new Map())
    }
    return this.caches.get(cacheName)!
  }

  clearCache(cacheName: string): void {
    if (this.caches.has(cacheName)) {
      this.caches.get(cacheName)!.clear()
      logger.debug(`: ${cacheName}`)
    }
  }

  clearAllCaches(): void {
    this.caches.forEach((cache, name) => {
      cache.clear()
      logger.debug(`: ${name}`)
    })
    logger.debug('')
  }

  getCacheStats(): Record<string, number> {
    const stats: Record<string, number> = {}
    this.caches.forEach((cache, name) => {
      stats[name] = cache.size
    })
    return stats
  }
}

// 
export const globalCacheManager = new CacheManager()

// 
export function measurePerformance<T>(
  operation: () => T,
  operationName: string
): T {
  const startTime = performance.now()
  const result = operation()
  const endTime = performance.now()
  const duration = endTime - startTime

  if (duration > 100) {
    logger.warn(` : ${operationName} (${duration.toFixed(2)}ms)`)
  } else {
    logger.debug(` : ${operationName} (${duration.toFixed(2)}ms)`)
  }

  return result
}

// 
export function logMemoryUsage(context: string): void {
  if (typeof performance !== 'undefined' && (performance as any).memory) {
    const memory = (performance as any).memory
    logger.debug(`  [${context}]:`, {
      used: `${(memory.usedJSHeapSize / 1048576).toFixed(2)}MB`,
      total: `${(memory.totalJSHeapSize / 1048576).toFixed(2)}MB`,
      limit: `${(memory.jsHeapSizeLimit / 1048576).toFixed(2)}MB`
    })
  }
}
