import { ref, computed, watch } from 'vue'
import { logger } from '@shared/utils/logger'

/**
 * 
 */
export function useCollapsibleItems() {
  // ID
  const collapsedItems = ref<Set<string>>(new Set())

  // LocalStorage
  const STORAGE_KEY = 'zero-code-collapsed-items'

  /**
   * LocalStorage
   */
  const loadCollapsedState = (): void => {
    try {
      // LocalStorage
      // const stored = localStorage.getItem(STORAGE_KEY)
      // if (stored) {
      //   const parsedIds = JSON.parse(stored) as string[]
      //   collapsedItems.value = new Set(parsedIds)
      //   logger.debug(':', parsedIds.length, '')
      // }
      
      // 
      collapsedItems.value = new Set()
      logger.debug('')
    } catch (error) {
      logger.error(':', error)
      collapsedItems.value = new Set()
    }
  }

  /**
   * LocalStorage
   */
  const saveCollapsedState = (): void => {
    try {
      const idsArray = Array.from(collapsedItems.value)
      localStorage.setItem(STORAGE_KEY, JSON.stringify(idsArray))
      logger.debug(':', idsArray.length, '')
    } catch (error) {
      logger.error(':', error)
    }
  }

  /**
   * 
   */
  const isCollapsed = (itemId: string): boolean => {
    return collapsedItems.value.has(itemId)
  }

  /**
   * 
   */
  const toggleCollapse = (itemId: string): void => {
    const newCollapsedItems = new Set(collapsedItems.value)
    
    if (newCollapsedItems.has(itemId)) {
      newCollapsedItems.delete(itemId)
      logger.debug(`: ${itemId}`)
    } else {
      newCollapsedItems.add(itemId)
      logger.debug(`: ${itemId}`)
    }
    
    collapsedItems.value = newCollapsedItems
  }

  /**
   * 
   */
  const collapseItem = (itemId: string): void => {
    if (!collapsedItems.value.has(itemId)) {
      const newCollapsedItems = new Set(collapsedItems.value)
      newCollapsedItems.add(itemId)
      collapsedItems.value = newCollapsedItems
      logger.debug(`: ${itemId}`)
    }
  }

  /**
   * 
   */
  const expandItem = (itemId: string): void => {
    if (collapsedItems.value.has(itemId)) {
      const newCollapsedItems = new Set(collapsedItems.value)
      newCollapsedItems.delete(itemId)
      collapsedItems.value = newCollapsedItems
      logger.debug(`: ${itemId}`)
    }
  }

  /**
   * 
   */
  const expandAll = (): void => {
    const count = collapsedItems.value.size
    collapsedItems.value = new Set()
    logger.debug(`: ${count}`)
  }

  /**
   * 
   */
  const collapseAll = (itemIds: string[]): void => {
    collapsedItems.value = new Set(itemIds)
    logger.debug(`: ${itemIds.length}`)
  }

  /**
   * 
   */
  const collapsedCount = computed(() => collapsedItems.value.size)

  /**
   * 
   */
  const getExpandedCount = (totalItems: number): number => {
    return totalItems - collapsedItems.value.size
  }

  // LocalStorage
  watch(
    () => collapsedItems.value,
    () => {
      saveCollapsedState()
    },
    { deep: true }
  )

  // LocalStorage
  loadCollapsedState()

  return {
    // 
    collapsedItems: computed(() => collapsedItems.value),
    collapsedCount,

    // 
    isCollapsed,
    toggleCollapse,
    collapseItem,
    expandItem,
    expandAll,
    collapseAll,
    getExpandedCount,

    // 
    loadCollapsedState,
    saveCollapsedState,
  }
}