<template>
  <div class="zero-code-modal-overlay" @click="$emit('close')">
    <div class="zero-code-modal-content" @click.stop>
      <div class="zero-code-modal-header">
        <h4></h4>
        <button class="zero-code-close-button" @click="$emit('close')"></button>
      </div>

      <div class="zero-code-modal-body">
        <!--  -->
        <div class="zero-code-img-detail-preview">
          <img :src="imageSrc" :alt="imageAlt">
          <div v-if="isImageUpdated(localImage)" class="zero-code-img-updated-badge">
            
          </div>
        </div>

        <!--  -->
        <div class="zero-code-form-section">
          <div class="zero-code-form-group">
            <label for="detail-image-name">:</label>
            <input
              id="detail-image-name"
              v-model="localImage.name"
              type="text"
              readonly
            >
          </div>

          <div class="zero-code-form-group">
            <label for="detail-image-alt">alt:</label>
            <input
              id="detail-image-alt"
              v-model="localImage.alt"
              type="text"
              readonly
            >
          </div>

          <div v-if="localImage.componentId" class="zero-code-form-group">
            <label for="detail-component-id">ID:</label>
            <input
              id="detail-component-id"
              v-model="localImage.componentId"
              type="text"
              readonly
            >
          </div>

          <!--  -->
          <div class="zero-code-img-detail-info">
            <div class="zero-code-img-detail-info-group">
              <label>:</label>
              <span class="zero-code-img-detail-category" :class="localImage.category">
                {{ localImage.category === 'common' ? '' : '' }}
              </span>
            </div>

            <div class="zero-code-img-detail-info-group">
              <label>:</label>
              <span>{{ formatFileSize(localImage.fileData?.size || localImage.size || 0) }}</span>
            </div>

            <div class="zero-code-img-detail-info-group">
              <label>:</label>
              <span>{{ localImage.fileData?.type || (localImage as any).type || '' }}</span>
            </div>

            <div class="zero-code-img-detail-info-group">
              <label>:</label>
              <span>{{ formatDateTime(localImage.uploadedAt) }}</span>
            </div>

            <div class="zero-code-img-detail-info-group">
              <label>URL:</label>
              <div class="zero-code-img-detail-url-group">
                <input
                  v-model="localImage.url"
                  type="text"
                  readonly
                  class="zero-code-img-detail-url-input"
                >
                <button
                  class="zero-code-btn zero-code-btn-secondary zero-code-copy-btn"
                  title="URL"
                  @click="copyToClipboard(localImage.url)"
                >
                  
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="zero-code-modal-footer">
        <div class="zero-code-modal-footer-left">
          <button
            class="zero-code-btn zero-code-btn-danger"
            @click="confirmDelete"
          >
            
          </button>
          <button
            class="zero-code-btn zero-code-btn-primary"
            @click="openReplaceDialog"
          >
            
          </button>
        </div>
        <div class="zero-code-modal-footer-right">
          <button class="zero-code-btn zero-code-btn-secondary" @click="$emit('close')">
            
          </button>
        </div>

        <!--  -->
        <input
          ref="fileInputRef"
          type="file"
          accept="image/*"
          style="display: none"
          @change="handleFileReplace"
        >
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
  import { ref, reactive, computed } from 'vue'
  import type { ImageData } from '@types'
  import { useImageDisplay } from '@/composables/useImageDisplay'

  // Props
  interface Props {
    image: ImageData
  }

  const props = defineProps<Props>()

  // Emits
  const emit = defineEmits<{
    close: []
    delete: [imageId: string]
    replace: [imageId: string, newImageData: ImageData]
  }>()

  // 
  const localImage = reactive<ImageData>({ ...props.image })
  const fileInputRef = ref<HTMLInputElement | null>(null)
  
  // 
  const { getImageSrc, getImageAlt, isImageUpdated, updateImageData } = useImageDisplay()
  
  // 
  const imageSrc = computed(() => getImageSrc(localImage))
  const imageAlt = computed(() => getImageAlt(localImage))

  // 
  const confirmDelete = () => {
    if (confirm('')) {
      emit('delete', localImage.id)
    }
  }

  // 
  const openReplaceDialog = () => {
    if (fileInputRef.value) {
      fileInputRef.value.click()
    }
  }

  // 
  const handleFileReplace = async (event: Event) => {
    const target = event.target as HTMLInputElement
    if (!target.files || !target.files[0]) return

    const file = target.files[0]
    
    if (!confirm(` "${file.name}" `)) {
      // 
      target.value = ''
      return
    }

    try {
      // Base64
      const base64 = await fileToBase64(file)
      
      // useImageDisplay
      const newImageData = updateImageData(localImage, file, base64.split(',')[1]) // data:image/...;base64, 
      
      // 
      Object.assign(localImage, newImageData)

      emit('replace', localImage.id, newImageData)
    } catch (error) {
      alert('')
    } finally {
      // 
      target.value = ''
    }
  }

  // Base64
  const fileToBase64 = (file: File): Promise<string> => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader()
      reader.readAsDataURL(file)
      reader.onload = () => resolve(reader.result as string)
      reader.onerror = error => reject(error)
    })
  }

  // 
  const copyToClipboard = async (text: string) => {
    try {
      await navigator.clipboard.writeText(text)
      alert('URL')
    } catch (error) {
      // 
      const textArea = document.createElement('textarea')
      textArea.value = text
      document.body.appendChild(textArea)
      textArea.select()
      document.execCommand('copy')
      document.body.removeChild(textArea)
      alert('URL')
    }
  }

  // 
  const formatFileSize = (bytes: number | undefined): string => {
    if (!bytes || isNaN(bytes)) return ''
    if (bytes === 0) return '0 Bytes'
    const k = 1024
    const sizes = ['Bytes', 'KB', 'MB', 'GB']
    const i = Math.floor(Math.log(bytes) / Math.log(k))
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
  }

  // 
  const formatDateTime = (dateString: string | undefined): string => {
    if (!dateString) return ''
    const date = new Date(dateString)
    if (isNaN(date.getTime())) return ''
    return date.toLocaleString('ja-JP', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
      hour: '2-digit',
      minute: '2-digit',
    })
  }
</script>

