<template>
  <div class="zero-code-modal-overlay" @click="$emit('close')">
    <div class="zero-code-part-modal" @click.stop>
      <div class="zero-code-modal-header">
        <div class="zero-code-modal-title">
          <div class="zero-code-title-main">
            <h3>{{ isEditing ? '' : '' }}</h3>
            <span :class="['zero-code-badge', partCategory]">
              {{ partCategory === 'common' ? '' : '' }}
            </span>
          </div>
          <p class="zero-code-modal-subtitle">
            {{ isEditing ? '' : '' }}
          </p>
        </div>
        <button class="zero-code-modal-close-btn" @click="$emit('close')">
          <span></span>
        </button>
      </div>

      <div class="zero-code-modal-body">
        <div class="zero-code-form-layout">
          <!-- :  -->
          <div class="zero-code-form-section">
            <div v-if="!isEditing" class="zero-code-form-group">
              <label for="part-type" class="zero-code-form-label">Type</label>
              <div class="zero-code-type-input-group">
                <select id="part-type" v-model="formData.type" class="zero-code-form-select">
                  <option value="">Type</option>
                  <option v-for="type in existingTypes" :key="type" :value="type">
                    {{ type }}
                  </option>
                </select>
                <input
                  v-if="!formData.type || !existingTypes.includes(formData.type)"
                  v-model="formData.type"
                  placeholder="Type"
                  class="zero-code-form-input"
                >
              </div>
            </div>

            <div v-if="!isEditing" class="zero-code-form-group">
              <label for="part-title" class="zero-code-form-label">Title</label>
              <input
                id="part-title"
                v-model="formData.title"
                placeholder=""
                class="zero-code-form-input"
              >
            </div>

            <div class="zero-code-form-group">
              <label for="part-body" class="zero-code-form-label">HTML Template</label>
              <div class="zero-code-body-editor-container">
                <textarea
                  id="part-body"
                  v-model="formData.body"
                  class="zero-code-body-editor"
                  placeholder="HTML..."
                  @input="handleBodyInput"
                />
              </div>
            </div>
          </div>

          <!-- :  -->
          <div class="zero-code-preview-section">
            <div class="zero-code-preview-header">
              <h4></h4>
              <span class="zero-code-preview-info"></span>
            </div>
            <div class="zero-code-preview-container">
              <!-- eslint-disable-next-line vue/no-v-html -->
              <div class="zero-code-preview-content" v-html="renderPartPreview(formData.body)" />
            </div>
          </div>
        </div>
      </div>

      <div class="zero-code-modal-footer">
        <div class="zero-code-modal-actions">
          <button class="zero-code-btn zero-code-btn-secondary" @click="$emit('close')">
            
          </button>
          <button
            class="zero-code-btn zero-code-btn-primary"
            :disabled="!isValid"
            @click="handleSave"
          >
            {{ isEditing ? '' : '' }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
  import { ref, computed } from 'vue'

  interface Props {
    isEditing?: boolean
    existingTypes: string[]
    partCategory?: 'common' | 'individual'
    editingPart?: {
      type: string
      module: {
        title: string
        body: string
      }
    } | null
    initialData?: {
      type: string
      title: string
      body: string
    }
    renderPartPreview: (body: string) => string
  }

  const props = withDefaults(defineProps<Props>(), {
    isEditing: false,
    partCategory: 'common',
    editingPart: null,
    initialData: () => ({
      type: '',
      title: '',
      body: '',
    }),
  })

  const emit = defineEmits<{
    (e: 'close'): void
    (e: 'save', data: { type: string; title: string; body: string; category: 'common' | 'individual' }): void
  }>()

  //  - editingPart
  const getInitialData = () => {
    if (props.isEditing && props.editingPart) {
      return {
        type: props.editingPart.type,
        title: props.editingPart.module.title,
        body: props.editingPart.module.body,
      }
    }
    return {
      type: props.initialData.type,
      title: props.initialData.title,
      body: props.initialData.body,
    }
  }

  const formData = ref(getInitialData())

  // 
  const isValid = computed(() => {
    if (props.isEditing) {
      return formData.value.body.trim() !== ''
    } else {
      return (
        formData.value.type.trim() !== '' &&
        formData.value.title.trim() !== '' &&
        formData.value.body.trim() !== ''
      )
    }
  })

  // 
  const handleBodyInput = (): void => {
    // 
  }

  // 
  const handleSave = (): void => {
    if (!isValid.value) return
    emit('save', {
      type: formData.value.type,
      title: formData.value.title,
      body: formData.value.body,
      category: props.partCategory || 'common',
    })
  }
</script>

